<template>
    <el-form  :model="form" label-width="90px" label-position="left">
        <el-row>
            <el-col :span="4">
                <el-form-item label=":">
                    <el-input v-model="form.mCode"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="4" :offset="1">
                <el-form-item label=":">
                    <el-input v-model="form.name"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="4" :offset="1">
                <el-form-item label=":">
                    <el-select v-model="form.state" placeholder="" >
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                    </el-select>
                </el-form-item>
            </el-col>
            <el-col :span="4" :offset="1">
                <el-button type="primary" @click="onSearch"> </el-button>
                <el-button @click="exportExcel('#memberTable','')"> </el-button>
            </el-col>
            <el-col :span="5" align="right">
                <router-link to="/addArrears">
                    <el-button  type="primary"></el-button>
                </router-link>
            </el-col>
        </el-row>          

        <el-row>
            <el-col :span="24">
                <el-table 
                    :data="searchData" 
                    id="memberTable" 
                    v-loading="loadingTable" 
                    element-loading-text=""
                    element-loading-spinner="el-icon-loading">
                    <el-table-column prop="mCode" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="mNickname" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="receivableBlance" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="currencyCode" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="status" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="bnsDeductPecent" label="" align="center">
                    </el-table-column>
                    <el-table-column label="" align="center" width="240px">
                        <template slot-scope="scope">
                            <el-button type="success" size="mini" @click="onShowDetails(scope.row)" style="padding:0 20px;"></el-button>
                            <el-button type="warning" size="mini" @click="DialogChangePercent(scope.row)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <br>
        <el-row type="flex" justify="center">
            <el-col :span="8" align="center">
                <el-pagination
                    :page-size="pageData.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :page-sizes="[10, 20, 30, 50,pageData.total]"
                    :total="pageData.total"
                    :current-page="pageData.currentPage"
                    @current-change="onChangePage"
                    @size-change="handleSizeChange">
                </el-pagination>
            </el-col>
        </el-row>

        <!--  -->
        <el-dialog title="?" :visible.sync="Dialog" width="400px" center>
            <el-row>
                <el-col :span="15">
                    <el-form-item>
                        <el-input v-model="percentage"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="1">
                    <el-form-item label-width="5px">
                    %
                    </el-form-item>
                </el-col>
            </el-row>  
            <span slot="footer" class="dialog-footer">
                <el-button @click="Dialog = false"> </el-button>
                <el-button type="primary" @click="onChangePercent"> </el-button>
            </span>
        </el-dialog>
    </el-form>
</template>


<script>
import {ToExportExcel} from "../../../util/util.js";
export default {
    data() {
        return {
            Dialog:false,
            percentage:null,  //
            ChangeData:null, //
            loadingTable:false, //
            timeAll:[], //
            form: {
                mCode:"",  //
                name: "", //
                state:"",   //
            },
            searchData: [], //
            //
            pageData:{
                currentPage:1,
                pageSize:10,
                total:null,
            }           
        };
    },
    methods: {
        //
        onChangePage(currentPage) {
            this.pageData.currentPage = currentPage;
            this.onSearch();
        },
        //
        handleSizeChange(pageSize) {
            this.pageData.pageSize = pageSize;
            this.onSearch();
        },
        //
        onSearch() {
            this.searchData = [];
            this.loadingTable = true; 
            let state = this.form.state;
            this.$axios({
                method:'post',
                url:"/apis/member/findReceivableAll",
                params:{
                    currentPage:this.pageData.currentPage,
                    pageSize:this.pageData.pageSize,
                    mCode:this.form.mCode,
                    mNickname:this.form.name,
                    status:this.form.state==""?0:(state==""?1:2),
                    date:new Date().getTime()
                }
            })     
            .then(response=>{
                if(response.data.code){
                    this.searchData = response.data.data.list;
                    for(var i = 0; i< this.searchData.length; i++ ){
                        this.searchData[i].bnsDeductPecent = this.searchData[i].bnsDeductPecent + "%";
                        if(this.searchData[i].status==1){
                            this.searchData[i].status="";
                        }else if(this.searchData[i].status==2){
                            this.searchData[i].status="";
                        }
                    }
                    this.pageData.currentPage = response.data.data.pageNum,
                    this.pageData.pageSize = response.data.data.pageSize,
                    this.pageData.total = response.data.data.total
                }
                setTimeout(()=>{
                    this.loadingTable = false;
                },200)
            })
        },
        //
        exportExcel() {                  
            if(this.searchData.length==0){
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                });
            }else {
                new Promise((resolve,reject)=>{
                    this.pageData.pageSize = this.pageData.total;
                    this.onSearch();
                    setTimeout(()=>{
                        resolve();
                    },500)
                })
                .then(()=>{
                    var wb = XLSX.utils.table_to_book(
                        document.querySelector("#memberTable")
                    );
                    var wbout = XLSX.write(wb, {
                        bookType: "xlsx",
                        bookSST: true,
                        type: "array"
                    });
                    try {
                        FileSaver.saveAs(
                            new Blob([wbout], { type: "application/octet-stream" }),
                            " .xlsx"
                        );
                    } catch (e) {
                        if (typeof console !== "undefined") console.log(e, wbout);
                    }
                    this.pageData.pageSize = 10;
                    this.onSearch();
                    return wbout;
                })
                
            }
        },
        //
        onShowDetails(row) {
            
        },
        //
        DialogChangePercent(row) {
            this.Dialog = true;
            this.ChangeData = row;
        },
        //
        onChangePercent() {
            // this.$axios({
            //     method:'post',
            //     url:"/apis/member/AuditReceivable",
            //     params:{
            //         mCode:this.current.mCode,
            //         mNickname:this.current.mNickname,
            //         transNumber:this.current.transNumber,
            //         status:3
            //     }
            // })     
            // .then(response=>{
            //     if(response.data.code){
            //         this.$message({
            //             showClose: true,
            //             message: '',
            //             type: 'success'
            //         });
            //         this.onSearch();
            //     }else{
            //         this.$message({
            //             showClose: true,
            //             message: response.data.msg,
            //             type: 'error'
            //         });
            //     }
            //     setTimeout(()=>{
            //         this.Dialog = false;
            //     },200)
            // })
        },
        //
        exportExcel(dom,title) {  
            if(this.searchData.length==0){
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                });
            }else {
                ToExportExcel(dom,title);       
            }
        },
    },
    created() {
        this.onSearch();
    }
};
</script>



