<template>
    <el-form status-icon :rules="rules" :model="form" ref="form" label-width="80px" label-position="left">

        <el-form-item label="">
            <el-button type="primary" icon="el-icon-search" @click="onSearch"></el-button>
        </el-form-item>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.id" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.name" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label=""  prop="nickname">
                    <el-input v-model="form.nickname"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                     <el-select v-model="form.sex" placeholder="">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                    </el-select>
                </el-form-item>
            </el-col>
        </el-row>


        <el-row>
            <el-col :span="6">
                <el-form-item label="Email">
                    <el-input v-model="form.email"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model.number="form.zipCode"></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="24">
                <el-form-item label="" class="inline-block" >
                    <div class="area" v-if="showArea">
                        <area-select type="text" :level="2" :placeholders="placeholders" v-model="form.address" :data="pcaa"></area-select>
                    </div>
                </el-form-item>
                <el-form-item label="" label-width="80px" class="text-center inline-block" >
                    <el-input v-model="form.detial" class="long-input"></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="18">
                <el-form-item label="">
                    <el-button type="success" size="mini" @click="addAddress"></el-button>
                    <el-table 
                        :data="addressTable" 
                        border 
                        size="mini"
                        v-loading="loadingTable" 
                        element-loading-text=""
                        element-loading-spinner="el-icon-loading">
                        <el-table-column type="index" align="center" width="50px">
                        </el-table-column>
                        <el-table-column prop="address" label="" align="center">
                        </el-table-column>
                        <el-table-column prop="name" label="" align="center" width="100px"> 
                        </el-table-column>
                        <el-table-column prop="tel" label="" align="center" width="150px">
                        </el-table-column>
                        <el-table-column prop="type" label="" align="center" width="110px">
                        </el-table-column>
                        <el-table-column label="" align="center" width="250px">
                            <template slot-scope="scope">
                                <el-button  size="mini" type="success"  @click="changeAddress(scope.row)"></el-button>
                                <el-button  size="mini" type="danger"  @click="removeAddress(scope.row)"></el-button>
                                <el-button  size="mini" type="primary"  @click="setDefaultAddress(scope.row)"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-form-item>
            </el-col>
        </el-row>
        
        <el-row>
            <el-col :span="8">
                <el-form-item label="" prop="desc">
                    <el-input type="textarea" :autosize="{ minRows: 4, maxRows: 6}"></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-form-item>
            <el-button type="primary" @click="onSubmit('form')" :loading="submitLoading"></el-button>
        </el-form-item>
    

        <!--  -->
        <dialog-com ref="dialog" @searchData="getSearchData" @addAddress="getAddress" @changeAddress="getChangeAddress"></dialog-com>

    </el-form>
</template>


<script>
import { pca, pcaa } from "area-data";

export default {
  data() {
    //
    var validateZipCode = (rule, value, callback) => {
        const reg = /^\d{6}$/;
        let isZipCode = reg.test(value);
        if (!isZipCode) {
            callback(new Error(''));
        } else {
            callback();
        }
    };
    return {
      placeholders: ["", "", ""],
      pca: pca,
      pcaa,
      submitLoading:false,  //loading
      loadingTable:false,   
      DialogVisible: false,
      showArea:true,
      searchUser: false,
      radio: "",
      oldForm:{},
      form: {
        id: "", //
        name: "", //
        nickname: "", //
        sex: "", //
        email: "", //
        zipCode: "", //
        address: [], //
        detial:"", //
        desc: "" //
      },
      //
      rules: {
        nickname: [
          { required: true, message: "", trigger: ['blur','change'] },
          { min: 1, max: 10, message: " 1  10 ", trigger: ['blur','change'] }
        ]
      },
      //
        addressTable:[],
    };
  },
  methods: {
    //
    onSubmit(form) {
        if(!this.form.id) {     //
            this.$message({
                showClose: true,
                message: '',
                type: 'error'
            });       
        }else{
            this.$refs[form].validate((valid) => {
                if (valid) {
                    //
                    if(JSON.stringify(this.form)===JSON.stringify(this.oldForm)){
                        this.$message({
                            showClose: true,
                            message: '!',
                            type: 'error'
                        }); 
                    }else {
                        this.submitLoading = true;
                        this.$axios({
                            method:'post',
                            url:"/apis/member/updateByMCodeAndMName",
                            params: {
                                mCode:this.form.id,
                                mName:this.form.name,
                                mNickname:this.form.nickname,
                                gender:this.form.sex==""?0:1,
                                email:this.form.email,
                                province:this.form.address[0],
                                city:this.form.address[1],
                                country:this.form.address[2],
                                detial:this.form.detial,
                                addPost:this.form.zipCode,
                                mDesc:this.form.desc
                            }
                        })
                        .then(response=>{
                            if(response.data.code){
                                this.$refs.dialog.userDefined({
                                    icon:"success",
                                    title:""
                                });
                                this.oldForm = JSON.parse(JSON.stringify(this.form));
                            } else{
                                this.$refs.dialog.userDefined({
                                    icon:"error",
                                    title:response.data.msg
                                });
                            }
                            this.submitLoading = false;
                        })    
                    }
                } else {
                    this.$message({
                        showClose: true,
                        message: '!',
                        type: 'error'
                    }); 
                    return false;
                }
            });
        }
    },
    
    // 
    addAddress() {
        if(this.form.id){
            this.$refs.dialog.showDialogAddAddress({
                id:this.form.id,
                zipCode:this.form.zipCode
            });
        }else {
            this.$message({
                showClose: true,
                message: '',
                type: 'error'
            });
        }
    },
    //
    removeAddress(row) {
        this.$confirm('?', '', {
            confirmButtonText: ' ',
            cancelButtonText: ' ',
            type: 'warning',
            center: true
        }).then(() => {
            this.$axios({
                method:'get',
                url:"/apis/member/delMemAddByAId",
                params: {
                    aId:row.id,
                    mCode:this.form.id
                }
            })
            .then(response=>{
                if(response.data.code){
                    this.$message({
                        message: '!',
                        type: 'success'
                    });
                    this.getAddressList();
                } else{
                    this.$message({
                        showClose: true,
                        message: '!',
                        type: 'error'
                    });
                }
            })    
        }).catch(() => {});
    },
    //
    changeAddress(row) {
        this.$refs.dialog.showDialogAddressChange({
            aId:row.id,
            mCode:this.form.id
        });
    },
    //
    onSearch() {
        this.showArea = false;
        this.form.address = [];  
        this.$refs.dialog.onSearchDialog();
        setTimeout(()=>{
            this.showArea = true;
        },300)
    },
    //
    getAddressList() {
        this.loadingTable = true;
        this.$axios({
            method:'get',
            url:"/apis/member/findAddAllByMCode",
            params: {
                mCode:this.form.id,
                date:new Date().getTime()
            }
        })
        .then(response=>{
            if(response.data.code){
                this.addressTable = [];
                for(var i = 0; i < response.data.data.length; i++){
                    let obj = {
                        address:response.data.data[i].addProvinceCode+"-"+response.data.data[i].addCityCode+"-"+response.data.data[i].addCountryCode+"-"+response.data.data[i].addDetial,
                        name:response.data.data[i].consigneeName,
                        tel:response.data.data[i].mobile,
                        type:response.data.data[i].defaultAdd==1?'':'',
                        id:response.data.data[i].aId
                    }
                    this.addressTable.push(obj);
                }

            } else{
                console.log("");
            }
            setTimeout(()=>{
                this.loadingTable = false;
            },200)
        })          
        
    },
    //
    setDefaultAddress(data) {
        this.$axios({
            method:'post',
            url:"/apis/member/defAddByAIdAndMCode",
            params: {
                aId:data.id,
                mCode:this.form.id,
                defaultAdd:1
            }
        })
        .then(response=>{
            if(response.data.code){
                //
                this.getAddressList();
            } else{
                console.log("")
            }
        })          
    },
    //
    getSearchData(data) {
        this.showArea = true;
        this.form.aId = data.mId,
        this.form.id = data.mCode;
        this.form.name = data.mName;
        this.form.nickname = data.mNickname;
        this.form.sex = data.gender==0?'':'';
        this.form.email = data.email;
        this.form.address = [data.province,data.city,data.country];
        this.form.detial = data.detial;
        this.form.zipCode = data.addPost;
        this.oldForm = JSON.parse(JSON.stringify(this.form));
        //
        this.getAddressList();
    },
    //
    getAddress(data){
        //
        this.getAddressList();
    },
    //
    getChangeAddress() {
        this.getAddressList();
    }
  }
};
</script>

<style>
.wrap .area .area-select-wrap .area-select {
  margin-left: 0;
  height: 28px;
}
.wrap .area-select.medium {
    width: 100px;
    margin-top: 7px;
}
.wrap .area-select-wrap .area-selected-trigger{
  line-height: 28px;
  text-align: center;
  padding: 0;
}
.wrap .area-select:hover{
    border-color: #02c1b3;
}
</style>



