<template>
    <el-form  label-width="80px" label-position="left">
        <el-row>
            <el-col :span="24">
                <el-table 
                    :data="tableData" 
                    border
                    v-loading="loadingTable" 
                    element-loading-text=","
                    element-loading-spinner="el-icon-loading">
                    <el-table-column prop="" label="" fixed align="center" width="80px">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center" width="80px"> 
                    </el-table-column>
                    <el-table-column prop="" label="" align="center" width="80px">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center" width="80px">
                    </el-table-column>
                    <el-table-column prop="" label="VIP" align="center" width="110px">
                    </el-table-column>
                    <el-table-column prop="" label=""  align="center" width="80px">
                    </el-table-column>
                    <el-table-column prop="" label="(PV)" align="center" width="110px">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center" width="110px">
                    </el-table-column>
                    <el-table-column prop="" label="(PV))" align="center" width="110px">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center" width="110px">
                    </el-table-column>
                    <el-table-column prop="" label="(PV)" align="center" width="110px">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center" width="110px">
                    </el-table-column>
                    <el-table-column prop="" label="(PV)" align="center" width="110px">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center" width="110px">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column label="" fixed="right" align="center" width="90px">
                        <template slot-scope="scope">
                            <el-button type="text"  @click="showDialogChange(scope.row,scope.$index)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <br>
        <el-row type="flex" justify="center">
            <el-col :span="8" align="center">
                <el-pagination
                    :page-size="pageData.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :page-sizes="[10, 20, 30, 50,pageData.total]"
                    :total="pageData.total"
                    :current-page="pageData.currentPage"
                    @current-change="onChangePage"  
                    @size-change="handleSizeChange">
                </el-pagination>
            </el-col>
        </el-row>
    </el-form>
</template>


<script>
export default {
    data() {
        return {
            loadingTable:false, //
            form: {
                id: "", //
                name: "", //
            },
            //
            tableData: [],
            //
            pageData:{
                currentPage:1,
                pageSize:10,
                total:null,
            },
        };
    },
    methods: {
        //
        onChangePage(currentPage) {
            this.form.currentPage = currentPage;
            //this.onSearch();
        },
        //
        handleSizeChange(pageSize) {
            this.pageData.pageSize = pageSize;
            //this.onSearch();
        },
        //
        onShowDetails(data) {
            
        }
    }
};
</script>
<style scoped>
.top-title span{
    display: inline-block;
    margin-right: 30px;
}
</style>




