<template>
    <div>
        <el-form>
            <el-form-item label="">
                <el-select v-model="periodCode" placeholder="" @change="selectChange" @focus="onFocus">
                    <el-option v-for="(item,index) in selectItem" :label="item" :value="item" :key="index"></el-option>
                </el-select>
            </el-form-item>
        </el-form>
           
        <el-row :gutter="20">
            <el-col :span="8">
                <el-card shadow="always">
                    <h4></h4>
                    <p>{{salesStatus}}</p>
                    <div class="botom-btn">
                        <router-link tag="a" target="_blank" to="/perStatus">
                            <el-button type="primary" @click="updata"> </el-button>
                        </router-link>
                    </div>
                </el-card>
            </el-col>
            <el-col :span="8">
                <el-card shadow="always">
                    <h4></h4>
                    <p>{{qualification}}</p>
                    <div class="botom-btn">
                        <el-button type="success" @click="DialogBack('')" :disabled="qualification!='' || achievement==''"> </el-button>
                        <router-link tag="a" target="_blank" :to="{path:'/qualification',query:{periodCode:periodCode}}">
                            <el-button type="primary" @click="updata"> </el-button>
                        </router-link>
                    </div>
                </el-card>
            </el-col>
        </el-row>
        <br>
        <el-row :gutter="20">
            <el-col :span="8">
                <el-card shadow="always">
                    <h4></h4>
                    <p>{{achievement}}</p>
                    <div class="botom-btn">
                        <el-button type="success" @click="DialogBack('')"> </el-button>
                        <router-link tag="a" target="_blank" :to="{path:'/achievement',query:{periodCode:periodCode}}">
                            <el-button type="primary" @click="updata"> </el-button>
                        </router-link>
                    </div>
                </el-card>
            </el-col>
            <el-col :span="8">
                <el-card shadow="always">
                    <h4></h4>
                    <p></p>
                    <div class="botom-btn">
                        <el-button type="success"> </el-button>
                        <router-link tag="a" target="_blank" :to="{path:'/bonus',query:{periodCode:periodCode}}">
                            <el-button type="primary" @click="updata"> </el-button>
                        </router-link>
                    </div>
                </el-card>
            </el-col>
        </el-row>
        <br>
        <el-row :gutter="20">
            <el-col :span="8">
                <el-card shadow="always">
                    <h4></h4>
                    <p></p>
                    <div class="botom-btn">
                        <el-button type="success"> </el-button>
                        <router-link tag="a" target="_blank" :to="{path:'/toExamineC',query:{periodCode:periodCode}}">
                            <el-button type="primary" @click="updata"> </el-button>
                        </router-link>
                    </div>
                </el-card>
            </el-col>
            <el-col :span="8">
                <el-card shadow="always">
                    <h4></h4>
                    <p></p>
                    <div class="botom-btn">
                        <el-button type="success"> </el-button>
                        <router-link tag="a" target="_blank" :to="{path:'/grant',query:{periodCode:periodCode}}">
                            <el-button type="primary" @click="updata"> </el-button>
                        </router-link>
                    </div>
                </el-card>
            </el-col>
        </el-row>
    </div>
</template>


<script>
import util from "../../../util/util.js";
export default {
    data() {
        return {
            Dialog:false,  
            periodCode:"", //
            selectItem:[],  //
            data:{},  //
            salesStatus:"",  //  
            qualification:"",//
            achievement:"", //
            title:"", //
            timer:null //
        };
    },
    methods: {
        //
        onFocus(event){
            window.clearInterval(this.timer);
        },
        //
        selectChange(val){
            window.clearInterval(this.timer);
            this.periodCode = val;
            this.onStatus()
        },
        //
        onStatus(){
            this.$axios({
                method:'post',
                url:"/apis/member/findQualificationAll",
                params:{
                    periodCode:this.periodCode,
                    currentPage:1,
                    pageSize:100,
                    date:new Date().getTime()
                }
            })      
            .then(response=>{
                this.salesStatus=""
                if(response.data.code){ 
                    this.qualification = ""
                    this.achievement = ""
                }else{
                    this.qualification = ""
                    this.achievement = ""
                }
            })
        },
        //
        onSearch() {
            this.selectItem = [];
            this.$axios({
                method:'get',
                url:"/apis/member/findPeriodAll",
                params:{
                    currentPage:1,
                    pageSize:1000,
                    date:new Date().getTime()
                }
            })     
            .then(response=>{
                if(response.data.code){
                    this.list = response.data.data.list
                    for(let i in this.list){
                        if(this.list[i].salesStatus >=2){
                            this.selectItem.push(this.list[i].periodCode);
                        }
                    }
                    this.periodCode = this.selectItem[0];
                }
                this.onStatus()
            })
        },
        //
        DialogBack(title) {
            this.$confirm(''+title+'?', '', {
                confirmButtonText: ' ',
                cancelButtonText: ' ',
                type: 'warning',
                center: true
            }).then(() => {
                this.onBack();
            }).catch(() => {});
        },
        //
        onBack(){
            let apis = "";
            if(this.title==""){
                apis = "backNowPeriodQualf";
            }else if(this.title==""){
                apis = "backNowPeriod";
            }

            this.$axios({
                method:'get',
                url:"/apis/member/"+apis,
                params:{
                    periodCode:this.periodCode,
                }
            })      
            .then(response=>{
                if(response.data.code) {
                    this.$message({
                        showClose: true,
                        message: response.data.msg,
                        type: 'success'
                    });
                    this.onSearch();  
                }else {
                    this.$message({
                        showClose: true,
                        message: response.data.msg,
                        type: 'error'
                    });
                }
            })
        },
        //
        updata() {
            // this.$alert(',', '', {
            //     confirmButtonText: '',
            //     showClose:false,
            //     center: true,
            //     dangerouslyUseHTMLString: true,
            //     callback: action => {
            //         this.onSearch();
            //     }
            // });
            this.timer = window.setInterval(()=>{
                this.onSearch();
            },3000)
        }
    },
    created() {
        this.onSearch();
    },
    destroyed() {
        window.clearInterval(this.timer);
    }
};
</script>

<style scoped>
.botom-btn{
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.el-card__body p{
    text-indent: 150px;
    line-height: 100px;
}
.botom-btn > a {
    margin-left: 15px;
}
.el-dialog__body .el-select {
    display: block;
}
</style>



