<template>
    <el-form  :model="form" label-width="80px" label-position="left">
        <el-row>
            <el-col :span="4">
                <el-form-item label="">
                    <el-input v-model="form.id"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="4" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.name"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="4" :offset="2">
                <el-form-item label=":" label-width="100px">
                    <el-select v-model="form.type" placeholder="">
                        <el-option v-for="(items,index) in select1" :key="index" :label="items" :value="items"></el-option>
                    </el-select>
                </el-form-item>
            </el-col>
        </el-row>      

        <el-row>
            <el-col :span="4">
                <el-form-item label="">
                    <el-select v-model="form.state" placeholder="">
                        <el-option v-for="(items,index) in select2" :key="index" :label="items" :value="items"></el-option>
                    </el-select>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-date-picker 
                        v-model="form.time" 
                        type="daterange" 
                        range-separator="-"
                        start-placeholder=""
                        end-placeholder=""
                        :default-time="['00:00:00', '23:59:59']"
                        :picker-options="pickerOptions"
                        @change="onChangeDate">
                    </el-date-picker>
                </el-form-item>
            </el-col>
            <el-col :span="4" :offset="2">
                <el-button type="primary" @click="onSearch"></el-button>
                <el-button @click="exportExcel('#memberTable','')"></el-button>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="24">
                <el-table 
                :data="searchData" 
                border 
                id="memberTable" 
                v-loading="loadingTable" 
                element-loading-text=""
                element-loading-spinner="el-icon-loading">
                    <el-table-column type="index" align="center" width="50px">
                    </el-table-column>
                     <el-table-column prop="mCode" label="" sortable align="center" width="140px">
                    </el-table-column>
                    <el-table-column prop="reviewStatus" label="" align="center" width="140px"> 
                    </el-table-column>
                    <el-table-column prop="mName" label="" align="center" width="140px">
                    </el-table-column>
                    <el-table-column prop="updateType" label="" align="center" width="140px">
                    </el-table-column>
                    <el-table-column prop="updateTime" label="" sortable align="center" width="160px">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center" width="140px">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center" width="140px">
                    </el-table-column>
                    <el-table-column prop="updateMemo" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="reviewMemo" label="" align="center">
                    </el-table-column>
                    <el-table-column label="" align="center" width="120px">
                        <template slot-scope="scope">
                            <el-button type="success" size="mini"  @click="onShowDetails(scope.row)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <br>
        <el-row type="flex" justify="center">
            <el-col :span="8" align="center">
                <el-pagination
                    :page-size="pageData.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :page-sizes="[10, 20, 30, 40,pageData.total]"
                    :total="pageData.total"
                    :current-page="pageData.currentPage"
                    @current-change="onChangePage"  
                    @size-change="handleSizeChange">
                </el-pagination>
            </el-col>
        </el-row>

        <!--  -->
        <dialog-com ref="dialog"></dialog-com>

    </el-form>
</template>


<script>
import {ToExportExcel} from "../../../util/util.js";
export default {
    data() {
        return {
            loadingTable:true, //
            select1:['','','','','','',''],
            select2:['','','','',''],
            form: {
                currentPage: "",  //
                id: "", //
                name: "", //
                type:"", //
                state:"", //
                time: ["",""] //
            },
            //
            searchData: [],
            //
            pageData:{
                currentPage:1,
                pageSize:20,
                total:null,
            },
            //
            pickerOptions: {
                shortcuts: [
                {
                    text: '',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                        picker.$emit('pick', [start, end]);
                    }
                },
                {
                    text: '',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                        picker.$emit('pick', [start, end]);
                    }
                }, 
                {
                    text: '',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                        picker.$emit('pick', [start, end]);
                    }
                }]
            }
        };
    },
    methods: {
        //
        onChangePage(currentPage) {
            this.form.currentPage = currentPage;
            this.onSearch();
        },
        //
        handleSizeChange(pageSize) {
            this.pageData.pageSize = pageSize;
            this.onSearch();
        },
        //
        onChangeDate( data) {
            if(data) {
                this.form.time[0] = data[0].getFullYear()+'-'+(data[0].getMonth()+1)+'-'+data[0].getDate()+' '+data[0].getHours()+':'+data[0].getMinutes()+':'+ data[0].getSeconds()
                this.form.time[1] = data[1].getFullYear()+'-'+(data[1].getMonth()+1)+'-'+data[1].getDate()+' '+data[1].getHours()+':'+data[1].getMinutes()+':'+ data[1].getSeconds()
            }
       },
        //
        onSearch() { 
            this.loadingTable = true; 
            //
            let reviewStatus = "";
            if(this.form.state==""){
                reviewStatus="";
            }else if(this.form.state==""){
                reviewStatus=3;
            }else if(this.form.state==""){
                reviewStatus=0;
            }else if(this.form.state==""){
                reviewStatus=2;
            }else if(this.form.state==""){
                reviewStatus=1;
            }
            //
            let Infotype = "";
            if(this.form.type==""){
                Infotype="";
            }else if(this.form.type==""){
                Infotype=0;
            }else if(this.form.type==""){
                Infotype=1;
            }else if(this.form.type==""){
                Infotype=2;
            }else if(this.form.type==""){
                Infotype=3;
            }else if(this.form.type==""){
                Infotype=4;
            }else if(this.form.type==""){
                Infotype=5;
            }
            this.$axios({
                method:'post',
                url:"/apis/member/findEditAll",
                params:{
                    currentPage:this.form.currentPage,
                    pageSize:this.pageData.pageSize,
                    mCode:this.form.id,
                    mName:this.form.name,
                    updateType:Infotype,
                    reviewStatus:reviewStatus,
                    updateTimeStar:this.form.time?this.form.time[0]:"",
                    updateTimeEnd:this.form.time?this.form.time[1]:"",
                    date:new Date().getTime()
                }
            })     
            .then(response=>{
                if(response.data.code){
                    this.searchData = response.data.data.list;
                    for(var i = 0; i< this.searchData.length; i++ ){
                        if(this.searchData[i].reviewStatus==0){
                            this.searchData[i].reviewStatus="";
                        }else if(this.searchData[i].reviewStatus==1){
                            this.searchData[i].reviewStatus="";
                        }else if(this.searchData[i].reviewStatus==2){
                            this.searchData[i].reviewStatus="";
                        }else{
                            this.searchData[i].reviewStatus="";
                        }

                        if(this.searchData[i].updateType==0){
                            this.searchData[i].updateType="";
                        }else if(this.searchData[i].updateType==1){
                            this.searchData[i].updateType="";
                        }else if(this.searchData[i].updateType==2){
                            this.searchData[i].updateType="";
                        }else if(this.searchData[i].updateType==3){
                            this.searchData[i].updateType="";
                        }else if(this.searchData[i].updateType==4){
                            this.searchData[i].updateType="";
                        }else if(this.searchData[i].updateType==5){
                            this.searchData[i].updateType="";
                        }else{
                            this.searchData[i].updateType="";
                        }
                    }
                    this.pageData.currentPage = response.data.data.pageNum,
                    this.pageData.pageSize = response.data.data.pageSize,
                    this.pageData.total = response.data.data.total
                }else {

                }
                setTimeout(()=>{
                    this.loadingTable = false;
                },200)
            })
        },
        //
        onShowDetails(data) {
            this.$refs.dialog.showDialoChangeDetails({
                data:data,
                showSubmit:false
            });
        },
        //
        exportExcel(dom,title) {  
            if(this.searchData.length==0){
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                });
            }else {
                ToExportExcel(dom,title);       
            }
        },
    },
    created() {
        this.onSearch();
    }
};
</script>

<style>
</style>



