<template>
    <div>
        <el-form ref="form" label-width="120px">
            <el-row>
                <el-col :span="9">
                    <el-form-item label="">
                        <el-button type="primary" @click="onSearch"></el-button>
                        <el-button type="primary" @click="onBinding" :loading="submitLoading"></el-button>
                        <el-button type="primary" @click="onAllBinding" :loading="submitLoading"></el-button>
                    </el-form-item>
                </el-col>
                <el-col :span="4">
                    <el-button type="success" @click="onBack"></el-button>
                </el-col>
            </el-row>
        </el-form>

        <el-row>
            <el-col :span="16">
                <el-table :data="tableData" border @selection-change="onSelect">
                    <el-table-column type="selection" align="center" width="50px">
                    </el-table-column>
                    <el-table-column prop="mCode" label="" align="center" width="130px">
                    </el-table-column>
                    <el-table-column prop="mCode" label="" align="center" width="140px">
                    </el-table-column>
                    <el-table-column prop="mName" label="" align="center" width="100px">
                    </el-table-column>
                    <el-table-column prop="idCode" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="creationer" label="" align="center" width="130px">
                    </el-table-column>
                    <el-table-column prop="creationIp" label="" align="center">
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <br>
        <el-row type="flex" justify="center">
            <el-col :span="8" >
                <el-pagination
                    :page-size="searchFrom.pageSize"
                    layout="total, sizes, prev, pager, next"
                    :page-sizes="[5, 10, 20, 30]"
                    :total="searchFrom.total"
                    :current-page="searchFrom.currentPage"
                    @current-change="onChangePage"
                    @size-change="handleSizeChange">
                </el-pagination>
            </el-col>
        </el-row>

        <!--  -->
        <dialog-com ref="dialog"></dialog-com>
    </div>
</template>


<script>
export default {
    data() {
        return {
            submitLoading:false,  //loading
            //
            searchFrom:{
                currentPage:1,
                pageSize:5,
                total:null,
                mCode:"",
                mName:"",
                mobile:"",
                mNickname:""
            },
            mCode:"",
            tableData:[],
        };
    },
    methods: {
        //
        onSearch() {   
            this.$axios({
                method:'post',
                url:"/apis/member/search",
                params:{
                    mCode:this.$route.query.code,
                    currentPage:1,
                    pageSize:10,
                    mName:"",
                    mobile:"",
                    mNickname:""
                }
            })     
            .then(response=>{
                if(response.data.data){
                    this.tableData = response.data.data.list;
                    for(var i = 0; i < this.tableData.length; i++){
                        this.tableData[i].code = this.$route.query.code;
                    }
                    this.searchFrom.total = response.data.data.total;
                }
            })
        },
        //
        onChangePage(currentPage) {
            this.searchFrom.currentPage = currentPage;
            this.onSearch();
        },
        //
        handleSizeChange(pageSize) {
            this.searchFrom.pageSize = pageSize;
            this.onSearch();
        },
        //
        onBinding() {
            if(this.tableData.length!=0) {     //
                this.submitLoading = true;
                this.$axios({
                    method:'post',
                    url:"/apis/member/updateBindingByMCode",
                    params: {
                        // mCode:this.form.id,		//
                        // mName:this.form.,		//
                        // idType:this.form,		//
                        // idCode:this.form,		//
                        // sponsorName:this.form,		//
                        // sponsorIdType:this.form,		//
                        // sponsorIdCode:this.form,		//
                        // raCode:this.form,		//
                        // raName:this.form,		//
                        // raIdType:this.form,		//
                        // raIdCode:this.form,		//
                        // rasponsorName:this.form,		//
                        // rasponsorCode:this.form,     //
                        // rasponsorIdType:this.form,		//
                        // rasponsorIdCode:this.form,		//
                    }
                })
                .then(response=>{
                    if(response.data.code){
                        this.$refs.dialog.userDefined({
                            icon:"success",
                            title:"!"
                        });
                    } else{
                        this.$refs.dialog.userDefined({
                            icon:"error",
                            title:response.data.msg
                        });
                    }
                    this.submitLoading = false;
                })   
            }else {
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });       
            }
        },
        //
        onAllBinding() {

        },
        onSelect(val){
            console.log(val)
        },
        //
        onBack() {
            this.$router.go(-1)
        }
    },
    created() {
        this.onSearch();
    }
    
};
</script>

<style>
.serch-input .el-form-item__content {
    position: relative;
}
.serch-input .el-icon-search {
    position: absolute;
    right: 10px;
    top: 12px;
    font-size: 18px;
    color: #666;
    cursor: pointer;
}
.el-icon-search:hover {
    color: #409eff;
}
.el-tag--danger {
    border: none;
}
</style>



