<template>
    <el-form  label-width="80px" label-position="left">
        <el-row type="flex" justify="space-between">
            <el-col :span="14" class="top-title">
                <span><b>{{periodCode}}</b></span>  
                <span>{{OrphanNum}}</span>
                <span>{{achNum}}</span>
                <span>{{sum}}</span>
            </el-col>
            <el-col :span="4" align="right">
                <el-button type="success" v-show="showOrphan" @click="onOrphan"></el-button>
                <el-button type="success" v-show="showAll" @click="onAll"></el-button>
                <el-button type="primary" :disabled="submitDisable"  @click="onCount"> </el-button>
            </el-col>
        </el-row>
        <br>
        <el-row>
            <el-col :span="24">
                <el-table 
                    :data="tableData" 
                    
                    v-loading="loadingTable" 
                    element-loading-text=","
                    element-loading-spinner="el-icon-loading">
                    <el-table-column prop="mCode" label="" fixed align="center">
                    </el-table-column>
                    <el-table-column prop="mName" label="" align="center"> 
                    </el-table-column>
                    <el-table-column prop="sponsorCode" label="" align="center" width="110">
                    </el-table-column>
                    <el-table-column prop="sponsorName" label="" align="center" width="110">
                    </el-table-column>
                    <el-table-column prop="mStatus" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="raStatus" label=""  align="center"  width="150">
                    </el-table-column>
                    <el-table-column prop="raShopYn" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="rankInit" label="" align="center"  width="110">
                    </el-table-column>
                    <el-table-column prop="ppv" label="PV" align="center"  width="140">
                    </el-table-column>
                    <el-table-column prop="ppvRetail" label="PV" align="center" width="140">
                    </el-table-column>
                    <el-table-column prop="appvInit" label="PV" align="center" width="140">
                    </el-table-column>
                    <el-table-column prop="appvFinal" label="PV" align="center" width="140">
                    </el-table-column>
                    <el-table-column prop="retailInit" label="" align="center" width="160">
                    </el-table-column>
                    <el-table-column prop="retail" label="" align="center" width="140">
                    </el-table-column>
                    <el-table-column prop="retailFinal" label="" align="center" width="160">
                    </el-table-column>
                    <el-table-column prop="ppvqualified" label="" align="center" width="140">
                    </el-table-column>
                    <el-table-column prop="rank" label="" align="center" width="140">
                    </el-table-column>
                    <el-table-column prop="orphan" label="" align="center">
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <br>
        <el-row type="flex" justify="center">
            <el-col :span="8" align="center">
                <el-pagination
                    :page-size="pageData.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :page-sizes="[10, 20, 30, 50,pageData.total]"
                    :total="pageData.total"
                    :current-page="pageData.currentPage"
                    @current-change="onChangePage"  
                    @size-change="handleSizeChange">
                </el-pagination>
            </el-col>
        </el-row>

    </el-form>
</template>


<script>
import util from "../../../util/util.js";
export default {
    data() {
        return {
            submitDisable:false, 
            showOrphan:true, //
            showAll:false,   //
            OrphanNum:0,    //
            achNum:0,    //
            sum:0, //
            loadingTable:false, //
            periodCode:"",  //
            //
            tableData: [],
            //
            pageData:{
                currentPage:1,
                pageSize:10,
                total:null,
            },
        };
    },
    methods: {
        //
        onSearch() {
            this.loadingTable = true;  
            this.$axios({
                method:'post',
                url:"/apis/member/findQualificationAll",
                params:{
                    periodCode:this.periodCode,
                    currentPage:this.pageData.currentPage,
                    pageSize:this.pageData.pageSize,
                    date:new Date().getTime()
                }
            })     
            .then(response=>{
                this.OrphanNum = 0;
                this.achNum = 0;
                this.sum = 0;
                if(response.data.code){
                    this.tableData = response.data.data.list;
                    this.pageData.total = response.data.data.total;
                    this.pageData.currentPage = response.data.data.pageNum;
                    this.pageData.pageSize = response.data.data.pageSize;
                    this.tableData = response.data.data.list;
                    for(var i in this.tableData){
                        this.achNum++;
                        this.sum += this.tableData[0].retailFinal;
                        //
                        if(this.tableData[i].mStatus==0){
                            this.tableData[i].mStatus = "";
                        }else if(this.tableData[i].mStatus==1){
                            this.tableData[i].mStatus = "";
                        }else if(this.tableData[i].mStatus==2){
                            this.tableData[i].mStatus = "";
                        }
                        //
                        if(this.tableData[i].raStatus==0){
                            this.tableData[i].raStatus = "";
                        }else if(this.tableData[i].raStatus==1){
                            this.tableData[i].raStatus = "";
                        }
                        //
                        if(this.tableData[i].raShopYn==0){
                            this.tableData[i].raShopYn = "";
                        }else if(this.tableData[i].raShopYn==1){
                            this.tableData[i].raShopYn = "";
                        }
                        //
                        if(this.tableData[i].rankInit==0){
                            this.tableData[i].rankInit = "";
                        }else if(this.tableData[i].rankInit==1){
                            this.tableData[i].rankInit = "VIP";
                        }else if(this.tableData[i].rankInit==2){
                            this.tableData[i].rankInit = "";
                        }else if(this.tableData[i].rankInit==3){
                            this.tableData[i].rankInit = "";
                        }else if(this.tableData[i].rankInit==4){
                            this.tableData[i].rankInit = "";
                        }else if(this.tableData[i].rankInit==5){
                            this.tableData[i].rankInit = "";
                        }else if(this.tableData[i].rankInit==6){
                            this.tableData[i].rankInit = "";
                        }else if(this.tableData[i].rankInit==7){
                            this.tableData[i].rankInit = "";
                        }else if(this.tableData[i].rankInit==8){
                            this.tableData[i].rankInit = "";
                        }else if(this.tableData[i].rankInit==9){
                            this.tableData[i].rankInit = "";
                        }
                        //
                        if(this.tableData[i].rank==0){
                            this.tableData[i].rank = "";
                        }else if(this.tableData[i].rank==1){
                            this.tableData[i].rank = "VIP";
                        }else if(this.tableData[i].rank==2){
                            this.tableData[i].rank = "";
                        }else if(this.tableData[i].rank==3){
                            this.tableData[i].rank = "";
                        }else if(this.tableData[i].rank==4){
                            this.tableData[i].rank = "";
                        }else if(this.tableData[i].rank==5){
                            this.tableData[i].rank = "";
                        }else if(this.tableData[i].rank==6){
                            this.tableData[i].rank = "";
                        }else if(this.tableData[i].rank==7){
                            this.tableData[i].rank = "";
                        }else if(this.tableData[i].rank==8){
                            this.tableData[i].rank = "";
                        }else if(this.tableData[i].rank==9){
                            this.tableData[i].rank = "";
                        }
                        //
                        if(this.tableData[i].ppvqualified==0){
                            this.tableData[i].ppvqualified = "";
                        }else if(this.tableData[i].ppvqualified==1){
                            this.tableData[i].ppvqualified = "";
                        }
                        //
                        if(this.tableData[i].orphan==0){
                            this.tableData[i].orphan = "";
                            this.OrphanNum ++;
                        }else{
                            this.tableData[i].orphan = "";
                        }   
                    }
                }
                setTimeout(()=>{
                    this.loadingTable = false;
                },200)
            })
        },
        //
        onCount() { 
            this.loadingTable = true;
            this.$axios({
                method:'get',
                url:"/apis/member/countNowQualification",
                params:{
                    periodCode:this.periodCode,
                }
            })     
            .then(response=>{
                if(response.data.code) {
                    setTimeout(()=>{
                        this.$message({
                            showClose: true,
                            message: '',
                            type: 'success'
                        });
                        this.onSearch();  
                    },1000)
                }else {
                    this.$message({
                        showClose: true,
                        message: response.data.msg,
                        type: 'error'
                    });
                    this.loadingTable = false;
                }
            })
        },
        //
        onOrphan() {
            let tempDate = [];
            for(var i in this.tableData){
                if(this.tableData[i].orphan==""){
                    tempDate.push(this.tableData[i]);
                }
            }
            this.tableData = tempDate;
            this.showOrphan = false;
            this.showAll = true;
        },
        //
        onAll() {
            this.onSearch();
            this.showAll = false;
            this.showOrphan = true;
        },
        //
        onChangePage(currentPage) {
            this.pageData.currentPage = currentPage;
            this.onSearch();
        },
        //
        handleSizeChange(pageSize) {
            this.pageData.pageSize = pageSize;
            this.onSearch();
        }
    },
    created() {
        this.periodCode = this.$route.query.periodCode;
        this.onSearch();
    },
    updated() {
        if(this.tableData.length==0){
            this.submitDisable = false;
        }else {
            this.submitDisable = true;
        }
    }
};
</script>
<style scoped>
.top-title span{
    display: inline-block;
    margin-right: 30px;
}
</style>




