<template>
    <el-form  :model="form" label-width="90px">
        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-date-picker 
                        v-model="form.time" 
                        type="daterange" 
                        range-separator="-"
                        start-placeholder=""
                        end-placeholder=""
                        :default-time="['00:00:00', '23:59:59']"
                        :picker-options="pickerOptions"
                        @change="onChangeDate">
                    </el-date-picker>
                </el-form-item>
            </el-col>
            <el-col :span="3" :offset="1">
                <el-form-item label=":">
                    <el-input v-model="form.id"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="3">
                <el-form-item label=":">
                    <el-input v-model="form.nickname"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="5" :offset="1" >
                <el-button type="primary" @click="onSearch"> </el-button>
                <el-button @click="exportExcel('#memberTable','')"> </el-button>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="4">
                <el-form-item label=":">
                    <el-input v-model="form.code"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="4" :offset="1" v-show="activeTag=='first'||activeTag=='third'">
                <el-form-item label=":">
                    <el-input v-model="form.recordNumber"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="4" :offset="1">
                <el-form-item label=":">
                    <el-select v-model="form.type" placeholder="" >
                        <el-option v-for="(item,index) in formSelect" :key="index" :label="item.label" :value="item.value"></el-option>
                    </el-select>
                </el-form-item>
            </el-col>
        </el-row>

        <template>
            <el-tabs v-model="activeTag" @tab-click="handleClick">
                <el-tab-pane label="" name="first">
                    <el-form  :model="formIntegral" label-width="100px">
                        <el-row v-if="form.id">
                            <el-col :span="4">
                                <el-form-item label=":">
                                    <el-input v-model="formIntegral.id" disabled></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="4" :offset="1">
                                <el-form-item label=":">
                                    <el-input v-model="formIntegral.nickname" disabled></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="4" :offset="1">
                                <el-form-item label=":">
                                    <el-input v-model="formIntegral.state" disabled></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row v-if="form.id">
                            <el-col :span="4">
                                <el-form-item label=":">
                                    <el-input v-model="formIntegral.reward" disabled></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="4" :offset="1">
                                <el-form-item label=":">
                                    <el-input v-model="formIntegral.shopping" disabled></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="4" :offset="1">
                                <el-form-item label=":">
                                    <el-input v-model="formIntegral.redemption" disabled></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="4">
                                <el-form-item label=":">
                                    <el-input v-model="formIntegral.rewardInto" disabled></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="4" :offset="1">
                                <el-form-item label=":">
                                    <el-input v-model="formIntegral.rewardOut" disabled></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-form>
                </el-tab-pane>
                <el-tab-pane label="" name="second">
                    <el-form  :model="formIntegral" label-width="100px">
                        <el-row v-if="form.id">
                            <el-col :span="4">
                                <el-form-item label=":">
                                    <el-input v-model="formIntegral.id" disabled></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="4" :offset="1">
                                <el-form-item label=":">
                                    <el-input v-model="formIntegral.nickname" disabled></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="4" :offset="1">
                                <el-form-item label=":">
                                    <el-input v-model="formIntegral.state" disabled></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row v-if="form.id">
                            <el-col :span="4">
                                <el-form-item label=":">
                                    <el-input v-model="formIntegral.reward" disabled></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="4" :offset="1">
                                <el-form-item label=":">
                                    <el-input v-model="formIntegral.shopping" disabled></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="4" :offset="1">
                                <el-form-item label=":">
                                    <el-input v-model="formIntegral.redemption" disabled></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="4">
                                <el-form-item label=":">
                                    <el-input v-model="formIntegral.shoppingInto" disabled></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="4" :offset="1">
                                <el-form-item label=":">
                                    <el-input v-model="formIntegral.shoppingOut" disabled></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-form>
                </el-tab-pane>
                <el-tab-pane label="" name="third">
                    <el-form  :model="formIntegral" label-width="100px">
                        <el-row v-if="form.id">
                            <el-col :span="4">
                                <el-form-item label=":">
                                    <el-input v-model="formIntegral.id" disabled></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="4" :offset="1">
                                <el-form-item label=":">
                                    <el-input v-model="formIntegral.nickname" disabled></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="4" :offset="1">
                                <el-form-item label=":">
                                    <el-input v-model="formIntegral.state" disabled></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row v-if="form.id">
                            <el-col :span="4">
                                <el-form-item label=":">
                                    <el-input v-model="formIntegral.reward" disabled></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="4" :offset="1">
                                <el-form-item label=":">
                                    <el-input v-model="formIntegral.shopping" disabled></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="4" :offset="1">
                                <el-form-item label=":">
                                    <el-input v-model="formIntegral.redemption" disabled></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="4">
                                <el-form-item label=":">
                                    <el-input v-model="formIntegral.redemptionInto" disabled></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="4" :offset="1">
                                <el-form-item label=":">
                                    <el-input v-model="formIntegral.redemptionOut" disabled></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-form>
                </el-tab-pane>
            </el-tabs>
        </template> 
        <br/>      

         <el-row>
            <el-col :span="24">
                <el-table 
                    :data="searchData" 
                    id="memberTable" 
                    v-loading="loadingTable" 
                    element-loading-text=""
                    element-loading-spinner="el-icon-loading">
                    <el-table-column prop="transNumber" label="" fixed align="center" width="100">
                    </el-table-column>
                    <el-table-column prop="batchNumber" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="mCode" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="mNickname" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="transDate" label="" align="center" width="160">
                    </el-table-column>
                    <el-table-column prop="transPeiod" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="transTypeCode" label="" align="center">
                    </el-table-column>
                    <el-table-column v-if="activeTag=='second'" prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column v-if="activeTag=='second'" prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column v-if="activeTag!='second'" prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="blanceBefore" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="blanceAfter" label="" align="center">
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <br>
        <el-row type="flex" justify="center">
            <el-col :span="8" align="center">
                <el-pagination
                    :page-size="pageData.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :page-sizes="[10, 20, 30, 50,pageData.total]"
                    :total="pageData.total"
                    :current-page="pageData.currentPage"
                    @current-change="onChangePage"  
                    @size-change="handleSizeChange">
                </el-pagination>
            </el-col>
        </el-row>
    </el-form>
</template>


<script>
import {ToExportExcel} from "../../util/util.js";
export default {
    data() {
        return {
            activeTag:"first", //
            formSelect:[], //
            formSelect1:[
                {
                    label:"",
                    value:""
                },
                {
                    label:"",
                    value:"INTOALL"
                },
                {
                    label:"",
                    value:"BA"
                },
                {
                    label:"",
                    value:"RC"
                },
                {
                    label:"",
                    value:"RB"
                },
                {
                    label:"",
                    value:"OUTALL"
                },
                {
                    label:"",
                    value:"WD"
                },
                {
                    label:"",
                    value:"SP"
                },
                {
                    label:"",
                    value:"RR"
                }
            ],
            formSelect2:[
                {
                    label:"",
                    value:""
                },
                {
                    label:"",
                    value:"INTOALL"
                },
                {
                    label:"",
                    value:"BT"
                },
                {
                    label:"",
                    value:"TF"
                },
                {
                    label:"",
                    value:"OT"
                },
                {
                    label:"",
                    value:"OUTALL"
                },
                {
                    label:"",
                    value:"OP"
                },
                {
                    label:"",
                    value:"TT"
                }
            ],
            formSelect3:[
                {
                    label:"",
                    value:"INTOALL"
                },
                {
                    label:"",
                    value:"PC"
                },
                {
                    label:"",
                    value:"OUTALL"
                },
                {
                    label:"",
                    value:"EG"
                }
            ],
            form: {
                id:"", //
                nickname: "", //
                timeStart:[],    //
                timeEnd:[],    //
                code:null,  //
                recordNumber:null,  //
                type:""  //
            },
            //
            formIntegral: {
                id:"", //
                nickname: "", //
                state: "", //
                reward: "", //
                shopping: "", //
                redemption: "", //
                rewardInto: "", //
                rewardOut: "", //
                shoppingInto: "", //
                shoppingOut: "", //
                redemptionInto: "", //
                redemptionOut: ""//
            },
            loadingTable:false, //
            searchData: [], //
            //
            pageData:{
                currentPage:1,
                pageSize:10,
                total:null,
            },
            //
            pickerOptions: {
                shortcuts: [{
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                    }
                },
                {
                    text: '',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                        picker.$emit('pick', [start, end]);
                    }
                }, 
                {
                    text: '',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                        picker.$emit('pick', [start, end]);
                    }
                }]
            }     
        };
    },
    methods: {
        //
        onChangePage(currentPage) {
            this.pageData.currentPage = currentPage;
            this.onSearch();
        },
        //
        handleSizeChange(pageSize) {
            this.pageData.pageSize = pageSize;
            this.onSearch();
        },
        //
        handleClick(tab) {
            if(tab.label==""){
                this.formSelect = this.formSelect1;
            }else if(tab.label==""){
                this.formSelect = this.formSelect2;
            }else if(tab.label==""){
                this.formSelect = this.formSelect3;
            }
            this.onSearch();
            if(this.form.id){
                this.findMIntegral();
            }
        },
        //
        onSearch() {
            this.searchData = [];
            this.loadingTable = true;  
            let transTimeS = "";
            if(this.form.time && this.form.time[0]!=""){
                transTimeS = this.form.time[0]+'/'+this.form.time[1];
            }else{
                transTimeS = "";
            }
            if(this.form.type==""){
                this.form.type = "";
            }
            if(this.form.id){
                this.findMIntegral();
            }
            this.$axios({
                method:'post',
                url:"/apis/member/findAccountLog",
                params:{
                    currentPage:this.pageData.currentPage,
                    pageSize:this.pageData.pageSize,
                    mCode:this.form.id,
                    mNickname:this.form.nickname,
                    transNumber:this.form.code,
                    transTimeS:transTimeS,
                    batchNumber:this.form.recordNumber,
                    transTypeCode:this.form.type,
                    typeS:this.activeTag=="first"?"BOP":(this.activeTag=="second"?"SHP":"PUI"),
                    date:new Date().getTime()
                }
            })     
            .then(response=>{
                if(response.data.code){
                    let searchData = response.data.data.list;
                    for(var i = 0; i< searchData.length; i++ ){
                        if(searchData[i].transTypeCode=="BA"){
                            searchData[i].transTypeCode="";
                        }else if(searchData[i].transTypeCode=="RC"){
                            searchData[i].transTypeCode="";
                        }else if(searchData[i].transTypeCode=="RB"){
                            searchData[i].transTypeCode="";
                        }else if(searchData[i].transTypeCode=="WD"){
                            searchData[i].transTypeCode="";
                        }else if(searchData[i].transTypeCode=="SP"){
                            searchData[i].transTypeCode="";
                        }else if(searchData[i].transTypeCode=="RR"){
                            searchData[i].transTypeCode="";
                        }else if(searchData[i].transTypeCode=="BT"){
                            searchData[i].transTypeCode="";
                        }else if(searchData[i].transTypeCode=="TF"){
                            searchData[i].transTypeCode="";
                        }else if(searchData[i].transTypeCode=="OT"){
                            searchData[i].transTypeCode="";
                        }else if(searchData[i].transTypeCode=="OP"){
                            searchData[i].transTypeCode="";
                        }else if(searchData[i].transTypeCode=="TT"){
                            searchData[i].transTypeCode="";
                        }else if(searchData[i].transTypeCode=="PC"){
                            searchData[i].transTypeCode="";
                        }else if(searchData[i].transTypeCode=="EG"){
                            searchData[i].transTypeCode="";
                        }
                    }
                    //
                    if(this.activeTag=="first"){
                        this.formIntegral.rewardInto = response.data.map.intoAll;
                        this.formIntegral.rewardOut = response.data.map.outAll;
                    }else if(this.activeTag=="second"){
                        this.formIntegral.shoppingInto = response.data.map.intoAll;
                        this.formIntegral.shoppingOut = response.data.map.outAll;
                    }else if(this.activeTag=="third"){
                        this.formIntegral.redemptionInto = response.data.map.intoAll;
                        this.formIntegral.redemptionOut = response.data.map.outAll;
                    }
                    this.searchData = searchData;
                    this.pageData.currentPage = response.data.data.pageNum,
                    this.pageData.pageSize = response.data.data.pageSize,
                    this.pageData.total = response.data.data.total
                }
                setTimeout(()=>{
                    this.loadingTable = false;
                },200)
            })
        },
       //
        onChangeDate(data) {
            if(data) {
                let month1 = data[0].getMonth()+1<10? "0"+ (data[0].getMonth()+1): data[0].getMonth()+1;
                let month2 = data[1].getMonth()+1<10? "0"+ (data[1].getMonth()+1): data[1].getMonth()+1;
                let day1 = data[0].getDate()<10? "0"+ data[0].getDate(): data[0].getDate();
                let day2 = data[1].getDate()<10? "0"+ data[1].getDate(): data[1].getDate();
                this.form.time[0] = data[0].getFullYear()+'-'+month1+'-'+ day1;
                this.form.time[1] = data[1].getFullYear()+'-'+month2+'-'+ day2;
            }
       },
       //
       findMIntegral() {
           this.formIntegral={
                id:"", //
                nickname: "", //
                state: "", //
                reward: "", //
                shopping: "", //
                redemption: "", //
                rewardInto: "", //
                rewardOut: "", //
                shoppingInto: "", //
                shoppingOut: "", //
                redemptionInto: "", //
                redemptionOut: ""//
            };
           this.$axios({
                method:'get',
                url:"/apis/member/findMemAccountByMCode",
                params:{
                    mCode:this.form.id,
                    date:new Date().getTime()
                }
            })     
            .then(response=>{
                if(response.data.code){
                    this.formIntegral.state = response.data.data.bonusStatus==0?"":(response.data.data.bonusStatus==1?"":"");
                    this.formIntegral.reward = response.data.data.bonusBlance;
                    this.formIntegral.shopping = response.data.data.walletBlance;
                    this.formIntegral.redemption = response.data.data.redemptionBlance;
                }else{
                    this.$message({
                        showClose: true,
                        message: '',
                        type: 'warning'
                    });
                }
            })

            this.$axios({
                method:'get',
                url:"/apis/member/findByMCode",
                params:{
                    mCode:this.form.id,
                    date:new Date().getTime()
                }
            })     
            .then(response=>{
                if(response.data.code){
                    this.formIntegral.id = response.data.data.memberBasic.mCode;
                    this.formIntegral.nickname = response.data.data.memberBasic.mNickname;
                }
            })
       },
        //
        exportExcel(dom,title) {  
            if(this.searchData.length==0){
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                });
            }else {
                ToExportExcel(dom,title);       
            }
        },
    },
    created() {
        this.formSelect = this.formSelect1;
        this.onSearch();
    }
};
</script>

<style scoped>
.el-tabs .el-form-item {
    margin-bottom: 0px;
}
</style>



