<template>
    <el-form  label-width="90px">
        <el-form-item label=":">
            <el-button type="primary" @click="onPassAll"></el-button>
        </el-form-item>
                     
         <el-row>
            <el-col :span="24">
                <el-table 
                    :data="searchData" 
                    id="memberTable" 
                    v-loading="loadingTable" 
                    element-loading-text=""
                    element-loading-spinner="el-icon-loading"
                    @selection-change="handleSelectionChange">
                    <el-table-column type="selection" width="55"></el-table-column>
                    <el-table-column prop="transDate" label="" align="center" width="140">
                    </el-table-column>
                    <el-table-column prop="mCode" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="mNickname" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="status" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="blanceBefore" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="amount" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="blanceAfter" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="bankCode" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="accCode" label="" align="center" width="160">
                    </el-table-column>
                    <el-table-column prop="accName" label="" align="center">
                    </el-table-column>
                    <el-table-column label="" fixed="right" align="center" width="140px">
                        <template slot-scope="scope">
                            <el-button type="success" size="mini" @click="onPass(scope.row)"> </el-button>
                            <el-button type="danger" size="mini" @click="onReject(scope.row)"> </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <br>
        <el-row type="flex" justify="center">
            <el-col :span="8" align="center">
                <el-pagination
                    :page-size="pageData.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :page-sizes="[10, 20, 30, 50,pageData.total]"
                    :total="pageData.total"
                    :current-page="pageData.currentPage"
                    @current-change="onChangePage"  
                    @size-change="handleSizeChange">
                </el-pagination>
            </el-col>
        </el-row>
    </el-form>
</template>


<script>
export default {
    data() {
        return {
            loadingTable:false, //
            searchData: [], //
            selectData: [], // 
            //
            pageData:{
                currentPage:1,
                pageSize:10,
                total:null,
            }
        };
    },
    methods: {
        //
        onChangePage(currentPage) {
            this.pageData.currentPage = currentPage;
            this.onSearch();
        },
        //
        handleSizeChange(pageSize) {
            this.pageData.pageSize = pageSize;
            this.onSearch();
        },
        //
        onSearch() {
            this.searchData = [];
            this.loadingTable = true;  
            this.$axios({
                method:'post',
                url:"/apis/member/findAccountLogWDALL",
                params:{
                    currentPage:this.pageData.currentPage,
                    pageSize:this.pageData.pageSize,
                    mCode:"",
                    mNickname:"",
                    transTimeS:"",
                    transNumber:null,
                    status:0,
                    date:new Date().getTime()
                }
            })     
            .then(response=>{
                if(response.data.code){
                    let searchData = response.data.data.list;
                    for(var i = 0; i< searchData.length; i++ ){
                        if(searchData[i].status==-2){
                            searchData[i].status="";
                        }else if(searchData[i].status==-1){
                            searchData[i].status="";
                        }else if(searchData[i].status==1){
                            searchData[i].status="";
                        }else if(searchData[i].status==2){
                            searchData[i].status="";
                        }else if(searchData[i].status==3){
                            searchData[i].status="";
                        }
                        //
                        searchData[i].bankCode = response.data.map.bank[i].bankCode;
                        searchData[i].accCode = response.data.map.bank[i].accCode;
                        searchData[i].accName = response.data.map.bank[i].accName;
                    }
                    this.searchData = searchData;
                    this.pageData.currentPage = response.data.data.pageNum,
                    this.pageData.pageSize = response.data.data.pageSize,
                    this.pageData.total = response.data.data.total
                }
                setTimeout(()=>{
                    this.loadingTable = false;
                },200)
            })
        },
        //
        onReject(row){
            this.$confirm('', '', {
                confirmButtonText: ' ',
                cancelButtonText: ' ',
                type: 'warning',
                center: true
            }).then(() => {
                this.$axios({
                    method:'post',
                    url:"/apis/member/updateAccLogWDOne",
                    params: {
                        mCode:row.mCode,
                        transNumber:row.transNumber,
                        status:-2,
                        date:new Date().getTime()
                    }
                })     
                .then(response=>{
                    if(response.data.code){
                        this.$message({
                            showClose: true,
                            message: '!',
                            type: 'success'
                        }); 
                    }else{
                        this.$message({
                            showClose: true,
                            message: '!',
                            type: 'error'
                        }); 
                    }
                    this.onSearch()
                })
            }).catch(() => {});
        },
        //
        onPass(row){
            this.$confirm('', '', {
                confirmButtonText: ' ',
                cancelButtonText: ' ',
                type: 'warning',
                center: true
            }).then(() => {
                this.$axios({
                    method:'post',
                    url:"/apis/member/updateAccLogWDOne",
                    params: {
                        mCode:row.mCode,
                        transNumber:row.transNumber,
                        status:3,
                        date:new Date().getTime()
                    }
                })     
                .then(response=>{
                    if(response.data.code){
                        this.$message({
                            showClose: true,
                            message: '!',
                            type: 'success'
                        }); 
                    }else{
                        this.$message({
                            showClose: true,
                            message: '!',
                            type: 'error'
                        }); 
                    }
                    this.onSearch()
                })
            }).catch(() => {});
        },
        //
        handleSelectionChange(row) {
            this.selectData = row;
            let tempData = "";
            for(let i = 0; i < row.length; i++){
                tempData += row[i].transNumber+"/";
            }
            this.selectData = tempData;
        },
        //
        onPassAll(){
            if(this.selectData.length>1){
                this.$confirm('', '', {
                    confirmButtonText: ' ',
                    cancelButtonText: ' ',
                    type: 'warning',
                    center: true
                }).then(() => {
                    this.$axios({
                        method:'post',
                        url:"/apis/member/updateAccLogWDAll",
                        params: {
                            transS:this.selectData,
                            date:new Date().getTime()
                        }
                    })     
                    .then(response=>{
                        if(response.data.code){
                            this.$message({
                                showClose: true,
                                message: '!',
                                type: 'success'
                            }); 
                        }else{
                            this.$message({
                                showClose: true,
                                message: '!',
                                type: 'error'
                            }); 
                        }
                        this.onSearch()
                    })
                }).catch(() => {});
            }else if(this.selectData.length==0){
                this.$message({
                    showClose: true,
                    message: '!',
                    type: 'error'
                }); 
            }else if(this.selectData.length<1){
                this.$message({
                    showClose: true,
                    message: '!',
                    type: 'error'
                }); 
            }
        }
    },
    created() {
        this.onSearch();
    }
};
</script>

<style scoped>
</style>



