<template>
    <el-form ref="form" status-icon :rules="rules" :model="form" label-width="100px" label-position="left">

        <el-form-item label="">
            <el-button type="primary" icon="el-icon-search"  @click="onSearch"></el-button>
        </el-form-item>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.id" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.name" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>
       
        <el-row>
            <el-col :span="6">
                <el-form-item label="" prop="tel">
                    <el-input v-model="form.tel" ></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="" prop="nickname">
                    <el-input v-model="form.nickname" ></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <template>
            <el-form-item label="" label-width="110px" >
                <el-radio v-model="form.withdrawDefault" label=""></el-radio>
            </el-form-item>
        </template>

        <el-row>
            <el-col :span="14">
                <el-form-item label="">
                    <el-button type="success" size="mini" @click="addBank"></el-button>
                    <el-table 
                        :data="allBankTable" 
                        border 
                        
                        v-loading="loadingTable" 
                        element-loading-text=""
                        element-loading-spinner="el-icon-loading">
                        <el-table-column prop="bankCode" label="" align="center" width="150px">
                        </el-table-column>
                        <el-table-column prop="accCode" label="" align="center"> 
                        </el-table-column>
                        <el-table-column prop="accName" label="" align="center" width="150px">
                        </el-table-column>
                        <el-table-column prop="accType" label="" align="center" width="100px">
                        </el-table-column>
                        <el-table-column label="" align="center" width="80px">
                            <template slot-scope="scope">
                                <el-button size="mini" type="danger" @click="untieBank(scope.row)"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-form-item>
            </el-col>
        </el-row>


        <el-row>
            <el-col :span="8">
                <el-form-item label="" prop="desc">
                    <el-input type="textarea" v-model="form.desc" :autosize="{ minRows: 4, maxRows: 6}" ></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-form-item>
            <el-button type="primary"  @click="onSubmit('form')" :loading="submitLoading"></el-button>
        </el-form-item>

        <!--  -->
        <dialog-com ref="dialog" @searchData="getSearchData" @addBank="getAddBank"></dialog-com>
    </el-form>
</template>


<script>
export default {
  data() {
    //
    var validateTel = (rule, value, callback) => {
        const reg = /^1[3|4|5|7|8][0-9]\d{8}$/;
        let isTel = reg.test(value);
        if ( !isTel) {
            callback(new Error(''));
        } else {
            callback();
        }
    };
    return {
        bankTable:[],       //
        submitLoading:false,  //loading
        loadingTable:false,
        oldForm:{},
        form: {
            id: "", //
            name: "", //
            nickname:"", //
            tel: "", //
            withdrawDefault: "", //
            desc: ""//
        },
        //
        rules: {
            nickname: [
                { required: true, message: "", trigger: "blur" },
                { min: 1, max: 10, message: " 1  10 ", trigger: "blur" }
            ],
            tel: [
                { required: true, message: "", trigger: "blur" },
                { validator: validateTel, trigger: 'blur'}
            ] 
        },
        //
        allBankTable: []
    };
  },
  methods: {
    //
    onSubmit(form) {  
        if(!this.form.id) {    //
            this.$message({
                showClose: true,
                message: '',
                type: 'error'
            });       
        }else{
            this.$refs[form].validate((valid) => {
                if (valid) {
                    //
                    if(JSON.stringify(this.form)===JSON.stringify(this.oldForm)){
                        this.$message({
                            showClose: true,
                            message: '!',
                            type: 'error'
                        }); 
                    }else {
                        this.submitLoading = true;
                        this.$axios({
                            method:'post',
                            url:"/apis/member/updateByMBank",
                            params: {
                                mCode:this.form.id,
                                mName:this.form.name,
                                mobile:this.form.tel,
                                mNickname:this.form.nickname,
                                withdrawDefault:this.form.withdrawDefault,
                                mDesc:this.form.desc
                            }
                        })
                        .then(response=>{
                            if(response.data.code){
                                this.$refs.dialog.userDefined({
                                    icon:"success",
                                    title:""
                                });
                                this.oldForm = JSON.parse(JSON.stringify(this.form));
                            } else{
                                this.$refs.dialog.userDefined({
                                    icon:"error",
                                    title:response.data.msg
                                });
                            }
                            this.submitLoading = false;
                        })
                    }
                } else {
                    this.$message({
                        showClose: true,
                        message: '!',
                        type: 'error'
                    }); 
                    return false;
                }
            });

             
            
        }
    },
    //
    onSearch() {
      this.$refs.dialog.onSearchDialog();
    },
    //
    getBankList() {
        this.loadingTable = true;
        this.$axios({
            method:'get',
            url:"/apis/member/findMBankByMCode",
            params: {
                mCode:this.form.id,
                date:new Date().getTime()
            }
        })
        .then(response=>{
            if(response.data.code){
                this.allBankTable = [];
                let leng = response.data.data.memberBank.length;
                let data = response.data.data.memberBank;
                for(var i = 0; i < leng; i++){
                    let obj = data[i];
                    this.allBankTable.push(obj);
                }
            } else{
                console.log("");
            }
            setTimeout(()=>{
                this.loadingTable = false;
            },200)
        })          
        
    },
    //
    untieBank(row) {
        this.$confirm('?', '', {
            confirmButtonText: ' ',
            cancelButtonText: ' ',
            type: 'warning',
            center: true
        }).then(() => {
            this.$axios({
                method:'get',
                url:"/apis/member/delBankByOid",
                params: {
                    oId:row.oId,
                    mCode:row.mCode
                }
            })
            .then(response=>{
                if(response.data.code){
                    this.$message({
                        showClose: true,
                        message: '!',
                        type: 'success'
                    });
                    this.getBankList();
                } else{
                    this.$message({
                        showClose: true,
                        message: '!',
                        type: 'error'
                    });
                }
            })
        }).catch(() => {});
    },
    //
    addBank() {
        if(this.form.id){
            this.$refs.dialog.showDialogBank(this.form.id);
        }else {
            this.$message({
                showClose: true,
                message: '',
                type: 'error'
            });
        }
    },
    //
    getSearchData(data) {
        this.form.desc = "";
        this.form.id = data.mCode;
        this.form.name = data.mName;
        this.form.tel = data.mobile;
        this.form.nickname = data.mNickname;
         this.oldForm = JSON.parse(JSON.stringify(this.form));
        this.getBankList();
    },
    //
    getAddBank() {
        this.getBankList();
    }
  }
};
</script>

<style>
.address .el-form-item__label {
  text-align: center;
}
</style>



