<template>
    <el-form ref="form" :rules="rules" :model="form" label-width="90px" label-position="left">

        <el-form-item label="">
            <el-button type="primary" icon="el-icon-search"  @click="onSearch"></el-button>
        </el-form-item>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.mcode" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.nickname" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>  

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.name" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.IDCode" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>   

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.currency" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.arrears" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>    

        <el-row>
            <el-col :span="6">
                <template>
                    <el-form-item label="" label-width="110px">
                        <el-radio v-model="form.type" label="" @input="changeMoney"></el-radio>
                        <el-radio v-model="form.type" label="" @input="changeMoney"></el-radio>
                    </el-form-item>
                </template>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.bankCode" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>   

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.money" @input="changeMoney"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.balance" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row> 

        <el-row>
            <el-col :span="4">
                <el-form-item label="" label-width="150px" prop="percentage">
                    <el-input v-model.number="form.percentage"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="1">
                <el-form-item label-width="5px">
                   %
                </el-form-item>
            </el-col>
        </el-row>      
        
        <el-row>
            <el-col :span="2" :offset="1">
                <el-button type="primary" :loading="submitLoading" @click="onSubmit('form')"></el-button>
            </el-col>
            <el-col :span="2">
                <router-link to="/memberArrears">
                    <el-button> </el-button>
                </router-link>
            </el-col>
        </el-row>

        <!--  -->
        <dialog-com ref="dialog" @searchData="getSearchData"></dialog-com>
    </el-form>
</template>


<script>
export default {
    data() {
        //
        var validate = (rule, value, callback) => {
            const reg = /^[1-9]{1,2}$/;
            let isRight = reg.test(value);
            if (!isRight) {
                callback(new Error('100'));
            } else {
                callback();
            }
        };
        return {
            submitLoading:false,  //loading
            form: {
                mcode: "", //
                name: "", //
                nickname: "", //
                IDCode: "", //
                currency:"", //
                arrears:null, //
                type: "", //
                bankCode: "", //
                money: null, //
                balance: "", //
                percentage: "", //
                oId:null,
            },
            //
            rules: {
                percentage: [
                    { validator: validate, trigger: ['blur','change'] }
                ]
            }
        };
    },
    methods: { 
        //
        onSubmit(form) {
            if(!this.form.mcode) {     //
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });       
            }else{
                this.$refs[form].validate((valid) => {
                    if (valid) {
                        this.submitLoading = true;
                        this.$axios({
                            method:'post',
                            url:"/apis/member/addReceivable",
                            params: {
                                mCode:this.form.mcode,
                                mNickname:this.form.nickname,
                                currencyCode:this.form.currency, 
                                receivableBlance:this.form.arrears,
                                trTypeCode:this.form.type==""?"NR":"RR",
                                oId:this.form.oId,
                                amount:this.form.money,
                                blanceAfter:this.form.balance,
                                bnsDeductPecent:this.form.percentage
                            }
                        })
                        .then(response=>{
                            if(response.data.code){
                                this.$refs.dialog.userDefined({
                                    icon:"success",
                                    title:""
                                });
                            } else{
                                this.$refs.dialog.userDefined({
                                    icon:"error",
                                    title:response.data.msg
                                });
                            }
                            this.submitLoading = false;
                        })
                    } else {
                        this.$message({
                            showClose: true,
                            message: '!',
                            type: 'error'
                        });
                        return false;
                    }
                }); 
            }
        },
        //
        onSearch() {
            this.$refs.dialog.onSearchDialog();
        },
        //
        getSearchData(data) {
            this.form = {
                mcode: data.mCode, //
                name: data.mNickname, //
                nickname: data.mName, //
                IDCode: data.idCode, //
                currency:"", //
                arrears:"", //
                type: "", //
                bankCode: "", //
                money: "", //
                balance: "", //
                percentage: "", //
            }
            this.getDetailed(data.mCode);
        },
        //
        getDetailed(mCdoe) {
            this.$axios({
                method:'get',
                url:"/apis/member/findReceivableByMCode",
                params: {
                    mCode:mCdoe,
                    date:new Date().getTime()
                }
            })
            .then(response=>{
                if(response.data.code){
                    this.form.currency = response.data.data.currencyCode;
                    this.form.arrears = response.data.data.receivableBlance;
                    this.form.bankCode = response.data.data.bank.accCode;
                    this.form.percentage = response.data.data.bnsDeductPecent;
                    this.form.oId = response.data.data.bank.oId;
                }
            })
        },
        //
        changeMoney(data) {
            if(this.form.type==""){
                this.form.balance = parseInt(this.form.arrears) - parseInt(this.form.money);
            }else {
                this.form.balance = parseInt(this.form.arrears) + parseInt(this.form.money);
            }
        }
    }
};
</script>

<style>
</style>



