<template>
    <el-form  label-width="80px" label-position="left">
        <el-row type="flex" justify="space-between">
            <el-col :span="13">
                <span><b>201808</b></span>  
            </el-col>
            <el-col :span="5" align="right">
                <el-button @click="back"> </el-button>
                <el-button type="primary"></el-button>
                <el-button type="primary"> </el-button>
            </el-col>
        </el-row>
        <br>
        <el-row type="flex" justify="center">
            <el-col :span="24">
                <el-table 
                    :data="tableData" 
                    v-loading="loadingTable" 
                    element-loading-text=","
                    element-loading-spinner="el-icon-loading">
                    <el-table-column prop="" label="" align="center" width="130px">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center" width="130px"> 
                    </el-table-column>
                    <el-table-column prop="" label="" align="center" width="130px">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center" width="140px">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center" width="140px">
                    </el-table-column>
                    <el-table-column prop="" label=""  align="center" width="140px">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center" width="140px">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center" width="140px">
                    </el-table-column>
                    <el-table-column prop="" label="PV" align="center">
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <br>
       <el-row>
            <el-col :span="24">
                <el-table 
                    :data="tableData" 
                    border
                    
                    v-loading="loadingTable" 
                    element-loading-text=""
                    element-loading-spinner="el-icon-loading">
                    <el-table-column prop="" label="" align="center" width="100px">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center" width="100px"> 
                    </el-table-column>
                    <el-table-column prop="" label="" align="center" width="100px">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center" width="130px">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center" width="130px">
                    </el-table-column>
                    <el-table-column prop="" label=""  align="center" width="130px">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="PV" sortable align="center">
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <br>
        <el-row type="flex" justify="center">
            <el-col :span="8" align="center">
                <el-pagination
                    :page-size="pageData.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :page-sizes="[10, 20, 30, 50,pageData.total]"
                    :total="pageData.total"
                    :current-page="pageData.currentPage"
                    @current-change="onChangePage"  
                    @size-change="handleSizeChange">
                </el-pagination>
            </el-col>
        </el-row>
    </el-form>
</template>


<script>
export default {
    data() {
        return {
            loadingTable:false, //
            form: {
                id: "", //
                name: "", //
            },
            //
            tableData: [],
            //
            pageData:{
                currentPage:1,
                pageSize:10,
                total:null,
            },
        };
    },
    methods: {
        //
        onChangePage(currentPage) {
            this.form.currentPage = currentPage;
            //this.onSearch();
        },
        //
        handleSizeChange(pageSize) {
            this.pageData.pageSize = pageSize;
            //this.onSearch();
        },
        //
        onShowDetails(data) {
            
        },
        //
        back() {
            this.$router.go(-1);
        }
        
    },
    created() {
        
    }
};
</script>




