<template>
    <el-form ref="form" :rules="rules"  :model="form" label-width="100px" label-position="left">

        <el-form-item label="">
            <el-button type="primary" icon="el-icon-search"  @click="onSearch"></el-button>
        </el-form-item>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.id" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.name" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.nickname" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.joinData" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.memberStatu" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.memberLevel" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.mobile" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.arrearsBalance" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.accountState" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.bonusPointsBalance" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.shoppingBalance" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.replacementBalance" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label="" prop="changeName">
                    <el-input v-model="form.changeName"></el-input>
                </el-form-item>
            </el-col>        
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label="" prop="IDType">
                    <el-select v-model="form.IDType" placeholder="">
                        <el-option label="" value="1"></el-option>
                        <el-option label="" value="2"></el-option>
                        <el-option label="" value="3"></el-option>
                        <el-option label="" value="4"></el-option>
                        <el-option label="" value="9"></el-option>
                    </el-select>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label="" prop="IDNumber">
                    <el-input v-model="form.IDNumber" ></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="20">
                <el-form-item label="" prop="file" v-show="form.id">
                    <el-upload 
                        action="/apis/member/uploadFile"
                        list-type="picture"
                        accept=".jpg, .png, .bmp"
                        :on-success="uploadSuccess"
                        :on-error="uploadError"
                        v-model="form.file"
                        :file-list="form.proofMaterial"
                        :before-upload="beforeUpload">
                        <div slot="tip" class="el-upload__tip">png/JPG/bmp5M</div>
                        <i class="el-icon-plus"></i>                       
                    </el-upload>
                    <el-dialog >
                        <img width="100%" alt="">
                    </el-dialog>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.changeNickname" ></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.changeMobile" ></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="16">
                <el-form-item label="">
                    <el-button type="success" size="mini"  @click="addBank"></el-button>
                    <el-button  type="success" size="danger" @click="onDialogRemoveBank" v-show="showRemoveBank"></el-button>
                </el-form-item>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="16">
                <el-form-item label="">
                    <el-table  
                        border             
                        :data="allBankTable"
                        v-loading="loadingTableBank" 
                        element-loading-text=""
                        element-loading-spinner="el-icon-loading">
                        <el-table-column prop="bankCode" label="" align="center">
                        </el-table-column>
                        <el-table-column prop="accCode" label="" align="center"> 
                        </el-table-column>
                        <el-table-column prop="accName" label="" align="center">
                        </el-table-column>
                        <el-table-column prop="accType" label="" align="center">
                        </el-table-column>
                        <el-table-column prop="defaultBank" label="" align="center">
                        </el-table-column>
                        <el-table-column label="" align="center" width="110px">
                            <template slot-scope="scope">
                                <el-button  size="mini" type="primary"  @click="setDefaultBank(scope.row)"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="16">
                <el-form-item label="">
                    <el-button type="success" size="mini" @click="addAddress"></el-button>
                    <el-button type="danger" size="mini" @click="onDialogRemoveAddress" v-show="showRemoveAddress"></el-button>
                </el-form-item>
            </el-col>
        </el-row>
        
        <el-row>
            <el-col :span="16">
                <el-form-item label="">
                    <el-table  
                        border 
                        :data="allAddressTable"
                        v-loading="loadingTableAddress" 
                        element-loading-text=""
                        element-loading-spinner="el-icon-loading">
                        <el-table-column label="#" align="center" type="index" width="60">
                        </el-table-column>
                        <el-table-column prop="address" label="" align="center"> 
                        </el-table-column>
                        <el-table-column prop="defaultAdd" label="" align="center" width="120px">
                        </el-table-column>
                        <el-table-column label="" align="center" width="110px">
                            <template slot-scope="scope">
                                <el-button  size="mini" type="primary"  @click="setDefaultAddress(scope.row)"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="8">
                <el-form-item label="" prop="remarks">
                    <el-input  v-model="form.remarks" type="textarea" :autosize="{ minRows: 4, maxRows: 6}"></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-form-item>
            <el-button type="primary" @click="onSubmit('form')" :loading="submitLoading"></el-button>
        </el-form-item>

         <!--  -->
        <dialog-com ref="dialog" @searchData="getSearchData" @addAddress="getAddress" @addBank="getAddBank"></dialog-com>

    </el-form>
</template>


<script>
import { pca, pcaa } from "area-data";
export default {
    data() {
        return {
            select:['','VIP','','','','','','',''],
            submitLoading:false,  //loading
            loadingTableBank:false,
            loadingTableAddress:false,
            dialogBankList: false,  //
            dialogAddressList:false,  //
            showRemoveBank: false, //
            showRemoveAddress: false, //
            placeholders: ["", "", ""],
            pca: pca,
            pcaa,
            allBankTable: [],  //
            allAddressTable: [],   //
            //
            newBankTable:[
                {
                    bankName:"",
                    bankId:"",
                    name:"",
                    type:"",
                }
            ],
            form: {
                id: "", //
                name: "", //
                nickname: "", //
                joinData:"",  //
                memberStatu:"",   //
                memberLevel:"", //
                mobile:"",  //
                arrearsBalance:"",  //
                accountState:"",    //
                bonusPointsBalance:"",  //
                shoppingBalance:"",  //
                replacementBalance:"",  //
                changeName:"",  //
                IDType:"",  //
                IDNumber:"",  //
                proofMaterial:[], //
                file:[], //
                changeNickname:"",  //
                changeMobile:"",  //
                addAddress:[],  //
                detial:"", //
                remarks:"",  //

            },
            //
            rules: {
                changeName: [
                    { required: true, message: "", trigger: ['blur','change'] },
                ],
                IDType: [
                    { required: true, message: "", trigger: ['blur','change'] },
                ],
                IDNumber: [
                    { required: true, message: "", trigger: ['blur','change'] },
                ],
                file: [
                     { required: true, message: "", trigger: ['blur','change'] },
                ],
                remarks: [
                    { required: true, message: "", trigger: ['blur','change'] },
                ]
            }
        };
    },
    methods: {
        //
        onSubmit(form) {
            if(!this.form.id) {     //
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });       
            }else{
                this.$refs[form].validate((valid) => {
                    if (valid) {
                        this.submitLoading = true;
                        this.$axios({
                            method:'post',
                            url:"/apis/member/updateIdByMCodeAndMName",
                            params: {
                                mCode:this.form.id,
                                mName:this.form.name,
                                idType: parseInt(this.form.IDType),
                                idCode: this.form.IDNumber,
                                mNickname: this.form.nickname,
                                mobile: this.form.mobile,
                                newMName:this.form.changeName,
                                uploadPath:this.form.file.join("||"),
                                mDesc:this.form.remarks
                            }
                        })
                        .then(response=>{
                            if(response.data.code){
                                this.$refs.dialog.userDefined({
                                    icon:"success",
                                    title:",!"
                                });
                            } else{
                                this.$refs.dialog.userDefined({
                                    icon:"error",
                                    title:response.data.msg
                                });
                            }
                            this.submitLoading = false;
                        })  
                    } else {
                        this.$message({
                            showClose: true,
                            message: '!',
                            type: 'error'
                        });
                        return false;
                    }
                }); 
            }
        },
        //
        onSearch() {
           this.$refs.dialog.onSearchDialog();
        },
        //
        getSearchData(data) {
            this.form.proofMaterial = [];
            this.form.file = [];
            this.form.addAddress = [];
            this.form.IDType = "";
            this.form.changeName = "";
            this.form.IDNumber = "";
            this.form.changeNickname = "";
            this.form.changeMobile = "";
            this.form.detial = "";
            this.form.remarks = "";
            this.allBankTable = [];
            this.allAddressTable = [];


            this.form.id = data.mCode;
            this.form.IDType = data.idType.toString();
            this.form.name = data.mName;
            this.form.nickname = data.mNickname;
            this.form.IDNumber = data.idCode;
            this.form.joinData = data.creationData;
            this.form.memberStatu = data.mStatus;
            this.form.memberLevel = data.mLevel;
            this.form.mobile = data.mobile;
            this.form.addPost = data.addPost;
            this.getMemberInfo();
            this.getBankList();
            this.getAddressList();
        },
        //
        getMemberInfo() {
            this.form.arrearsBalance = "";
            this.form.accountState = "";
            this.form.bonusPointsBalance = "";
            this.form.shoppingBalance = "";
            this.form.replacementBalance = "";
            this.$axios({
                method:'get',
                url:"/apis/member/findMemAccountByMCode",
                params: {
                    mCode:this.form.id,
                    date:new Date().getTime()
                }
            })
            .then(response=>{
                if(response.data.code){
                    this.form.arrearsBalance = response.data.data.bonusBlance;
                    this.form.accountState = response.data.data.bonusStatus;
                    this.form.bonusPointsBalance = response.data.data.bonusBlance;
                    this.form.shoppingBalance = response.data.data.walletBlance;
                    this.form.replacementBalance = response.data.data.redemptionBlance;
                    this.isChangeFrom = false;
                }
            })    
        },
        //
        getBankList() {
            this.loadingTableBank = true;
            this.$axios({
                method:'get',
                url:"/apis/member/findMBankByMCode",
                params: {
                    mCode:this.form.id,
                    date:new Date().getTime()
                }
            })
            .then(response=>{
                if(response.data.data.memberBank.length != 0){
                    this.showRemoveBank = true; 
                    this.allBankTable = response.data.data.memberBank;
                    for(let i in this.allBankTable){
                        if(this.allBankTable[i].defaultBank==0){
                            this.allBankTable[i].defaultBank="";
                        }else{
                            this.allBankTable[i].defaultBank="";
                        }
                    }
                }else {
                    this.showRemoveBank = false; 
                }
                setTimeout(()=>{
                    this.loadingTableBank = false;
                },200)
            }) 
        },
        //
        onDialogRemoveBank() {
            if(this.form.id){
                this.$confirm('?', '', {
                    confirmButtonText: ' ',
                    cancelButtonText: ' ',
                    type: 'warning',
                    center: true
                }).then(() => {
                     this.onRemoveBank();
                }).catch(() => {});
            }else {
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });
            }
        },
        //
        onRemoveBank() {
            this.$axios({
                method:'get',
                url:"/apis/member/outAllBankByMCode",
                params: {
                    mCode:this.form.id
                }
            })
            .then(response=>{
                if(response.data.code){
                    this.$message({
                        showClose: true,
                        message: '!',
                        type: 'success'
                    });
                    this.allBankTable = [];
                    this.showRemoveBank = false; 
                    this.isChangeFrom = true;
                }else {
                    this.$message({
                        showClose: true,
                        message: '!',
                        type: 'error'
                    });
                }
            }) 
        },
        //
        addBank() {
            if(this.form.id){
                this.$refs.dialog.showDialogBank(this.form.id);
                this.isChangeFrom = true;
            }else {
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });
            }
        },
        //
        setDefaultBank(data) {
            this.$axios({
                method:'post',
                url:"/apis/member/defBankByOid",
                params: {
                    oId:data.oId,
                    mCode:this.form.id,
                    defaultBank:1
                }
            })
            .then(response=>{
                if(response.data.code){
                    //
                    this.getBankList();
                } else{
                    this.$message({
                        showClose: true,
                        message: '',
                        type: 'error'
                    });
                }
            })          
        },
        //
        getAddBank(){
            this.getBankList();
        },
        //
        addAddress() {
            if(this.form.id){
                this.$refs.dialog.showDialogAddAddress({
                    id:this.form.id,
                    zipCode:this.form.addPost
                });
            }else {
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });
            }
        },
        //
        getAddress(data){
            //
            this.getAddressList();
        },
        //
        getAddressList() {
            this.loadingTableAddress = true;
            this.$axios({
                method:'get',
                url:"/apis/member/findAddAllByMCode",
                params: {
                    mCode:this.form.id,
                    date:new Date().getTime()
                }
            })
            .then(response=>{
                if(response.data.data.length!=0){
                    this.showRemoveAddress = true;
                    this.allAddressTable = [];
                    this.allAddressTable = response.data.data;
                    for(var i = 0; i < this.allAddressTable.length; i++){
                        this.allAddressTable[i].address = this.allAddressTable[i].addProvinceCode+"-"+this.allAddressTable[i].addCityCode+"-"+this.allAddressTable[i].addCountryCode+"-"+this.allAddressTable[i].addDetial;
                        this.allAddressTable[i].defaultAdd = this.allAddressTable[i].defaultAdd==0?"":"";
                    }
                } else{
                    //
                    this.allAddressTable = [];
                    this.showRemoveAddress = false;
                }
                setTimeout(()=>{
                    this.loadingTableAddress = false;
                },200)
            })  
        },
        //
        setDefaultAddress(data) {
            this.$axios({
                method:'post',
                url:"/apis/member/defAddByAIdAndMCode",
                params: {
                    aId:data.aId,
                    mCode:this.form.id,
                    defaultAdd:1
                }
            })
            .then(response=>{
                if(response.data.code){
                    //
                    this.getAddressList();
                } else{
                    this.$message({
                        showClose: true,
                        message: '',
                        type: 'error'
                    });
                }
            })    
        },
        //
        onDialogRemoveAddress() {
            if(this.form.id){
                this.$confirm('?', '', {
                    confirmButtonText: ' ',
                    cancelButtonText: ' ',
                    type: 'warning',
                    center: true
                }).then(() => {
                     this.onRemoveAddress();
                }).catch(() => {});
            }else {
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });
            }
        },
        //
        onRemoveAddress() {
            this.$axios({
                method:'get',
                url:"/apis/member/outAllAddByMCode",
                params: {
                    mCode:this.form.id
                }
            })
            .then(response=>{
                if(response.data.code){
                    this.$message({
                        showClose: true,
                        message: '!',
                        type: 'success'
                    });
                    this.allAddressTable = [];
                    this.showRemoveAddress = false;
                    this.isChangeFrom = true;
                }else {
                    this.$message({
                        showClose: true,
                        message: '!',
                        type: 'error'
                    });
                }
            }) 
        },
        //
        uploadSuccess(response) {
            if(response.code){
                this.form.file.push(response.data)
            }
            this.$message({
                showClose: true,
                message: '',
                type: 'success'
            });
        },
        //
        uploadError() {
            this.$message({
                showClose: true,
                message: ',',
                type: 'error'
            });
        },
        //
        beforeUpload(file) {
            const fileType = file.type=="image/jpeg"||file.type=="image/png"||file.type=="image/bmp";
            const fileSize = file.size / 1024 / 1024 <= 5;
            if(!fileType){
                this.$message({
                    showClose: true,
                    message: 'png/JPG/bmp',
                    type: 'error'
                });
            }
            if(!fileSize){
                this.$message({
                    showClose: true,
                    message: '5M',
                    type: 'error'
                });
            }
            return fileType && fileSize;
        }
    }
};
</script>

<style>
.wrap i.el-icon-plus{
    font-size: 32px;
    color: #c3c3c3;
}
.wrap .el-upload-dragger {
    width: 34px;
    height: 34px;
    line-height: 34px;
}
.wrap .el-upload--picture {
    width: 34px;
    height: 34px;
    line-height: 34px;
    border: 1px solid #ccc;
}
.wrap .el-upload-list--picture .el-upload-list__item.is-success {
    display: inline-block;
    margin-right: 10px;
    padding:0;
    width:46px;
    height: 46px;
}
.wrap .el-upload-list--picture .el-upload-list__item.is-success img{
    width:100%;
    height: 100%;
    z-index: 0;
    margin: 0;
}
.wrap .el-upload-list__item .el-icon-close {
    right: -19px;
    top: -10px;
    opacity: 1;
    text-align: center;
    line-height: 36px;
    width: 46px;
    font-size: 12px;
    height: 26px;
    transform: rotate(45deg);
    box-shadow: 0 1px 1px #ccc;
    color:#fff;
    background:red;
}
.wrap .el-upload-list__item .el-icon-close:before {
    transform: rotate(-45deg);
    display: inline-block;
}
.wrap .el-upload-list--picture .el-upload-list__item-status-label {
    right: -19px;
    top: -10px;
}
.wrap .el-upload-list--picture-card .el-upload-list__item-status-label i {
    transform: scale(0.8) rotate(-45deg);
}
.wrap .el-upload__tip {
    line-height: 20px;
    display: inline-block;
    margin-left: 10px;
}
.wrap .el-icon-close-tip{
    opacity: 0;
}
.wrap .area-select.medium {
    width: 100px;
    margin-top: 7px;
}
.wrap .area-select-wrap .area-selected-trigger{
  line-height: 28px;
  text-align: center;
  padding: 0;
}
.wrap .area-select:hover{
    border-color: #409EFF;
}
</style>



