<template>
    <el-form  :model="form" label-width="90px">
        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-date-picker 
                        v-model="form.time" 
                        type="daterange" 
                        range-separator="-"
                        start-placeholder=""
                        end-placeholder=""
                        :default-time="['00:00:00', '23:59:59']"
                        :picker-options="pickerOptions"
                        @change="onChangeDate">
                    </el-date-picker>
                </el-form-item>
            </el-col>
            <el-col :span="3" :offset="1">
                <el-form-item label=":">
                    <el-input v-model="form.id"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="3">
                <el-form-item label=":">
                    <el-input v-model="form.name"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="5" :offset="1" >
                <el-button type="primary" @click="onSearch"> </el-button>
                <el-button @click="exportExcel('#memberTable','')"> </el-button>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="4">
                <el-form-item label=":">
                    <el-input v-model="form.code"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="4" :offset="1">
                <el-form-item label=":">
                    <el-select v-model="form.type" placeholder="" >
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                    </el-select>
                </el-form-item>
            </el-col>
            <el-col :span="4" :offset="1">
                <el-form-item label=":">
                    <el-select v-model="form.state" placeholder="" >
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                    </el-select>
                </el-form-item>
            </el-col>
        </el-row>          

         <el-row>
            <el-col :span="24">
                <el-table 
                    :data="searchData" 
                    id="memberTable" 
                    v-loading="loadingTable" 
                    element-loading-text=""
                    element-loading-spinner="el-icon-loading">
                    <el-table-column prop="transNumber" label="" fixed align="center" width="100">
                    </el-table-column>
                    <el-table-column prop="batchNumber" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="mCode" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="mNickname" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="trTypeCode" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="trSourceType" label="" align="center" width="140">
                    </el-table-column>
                    <el-table-column prop="accType" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="bankCode" label="" align="center" width="100">
                    </el-table-column>
                    <el-table-column prop="accCode" label="" align="center" width="160">
                    </el-table-column>
                    <el-table-column prop="currencyCode" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="blanceBefore" label="" align="center" width="90">
                    </el-table-column>
                    <el-table-column prop="amount" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="blanceAfter" label="" align="center" width="90">
                    </el-table-column>
                    <el-table-column prop="transDate" label="" align="center" width="150">
                    </el-table-column>
                    <el-table-column prop="transRepiod" label="" align="center" width="110">
                    </el-table-column>
                    <el-table-column prop="transDesc" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="status" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="washedYn" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="orignTransNumber" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="creationBy" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="transDate" label="" align="center" width="150">
                    </el-table-column>
                    <el-table-column prop="updateBy" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="updateTime" label="" align="center" width="150">
                    </el-table-column>
                    <el-table-column prop="autohrizeBy" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="autohrizeTime" label="" align="center" width="150">
                    </el-table-column>
                    <el-table-column prop="autohrizeDesc" label="" align="center">
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <br>
        <el-row type="flex" justify="center">
            <el-col :span="8" align="center">
                <el-pagination
                    :page-size="pageData.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :page-sizes="[10, 20, 30, 50,pageData.total]"
                    :total="pageData.total"
                    :current-page="pageData.currentPage"
                    @current-change="onChangePage"  
                    @size-change="handleSizeChange">
                </el-pagination>
            </el-col>
        </el-row>
    </el-form>
</template>


<script>
import {ToExportExcel} from "../../../util/util.js";
export default {
    data() {
        return {
            form: {
                id:"", //
                name: "", //
                timeStart:[],    //
                timeEnd:[],    //
                code:null,  //
                state:"",   //
                type:""  //
            },
            loadingTable:false, //
            searchData: [], //
            //
            pageData:{
                currentPage:1,
                pageSize:10,
                total:null,
            },
            //
            pickerOptions: {
                shortcuts: [{
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                    }
                },
                {
                    text: '',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                        picker.$emit('pick', [start, end]);
                    }
                }, 
                {
                    text: '',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                        picker.$emit('pick', [start, end]);
                    }
                }]
            }     
        };
    },
    methods: {
        //
        onChangePage(currentPage) {
            this.pageData.currentPage = currentPage;
            this.onSearch();
        },
        //
        handleSizeChange(pageSize) {
            this.pageData.pageSize = pageSize;
            this.onSearch();
        },
        //
        onSearch() {
            this.searchData = [];
            this.loadingTable = true;  
            let transTimeS = "";
            let type = "";
            if(this.form.type==""){
                type= "";
            }else if(this.form.type==""){
                type= "NR";
            }else if(this.form.type==""){
                type="RR";
            }
        
            if(this.form.time && this.form.time[0]!=""){
                transTimeS = this.form.time[0]+'/'+this.form.time[1];
            }else{
                transTimeS = "";
            }
    
            this.$axios({
                method:'post',
                url:"/apis/member/findReceivableDetailAll",
                params:{
                    currentPage:this.pageData.currentPage,
                    pageSize:this.pageData.pageSize,
                    mCode:this.form.id,
                    mNickname:this.form.name,
                    transTimeS:transTimeS,
                    transNumber:this.form.code,
                    status:0,
                    trTypeCode:type,
                    date:new Date().getTime()
                }
            })     
            .then(response=>{
                if(response.data.code){
                    let searchData = response.data.data.list;
                    for(var i = 0; i< searchData.length; i++ ){
                        if(searchData[i].trTypeCode=="NR"){
                            searchData[i].trTypeCode="";
                        }else if(searchData[i].trTypeCode=="RR"){
                            searchData[i].trTypeCode="";
                        }

                        if(searchData[i].trSourceType=="CMP"){
                            searchData[i].trSourceType="";
                        }else if(searchData[i].trSourceType=="OBB"){
                            searchData[i].trSourceType="";
                        }else if(searchData[i].trSourceType=="OWB"){
                            searchData[i].trSourceType="";
                        }else if(searchData[i].trSourceType=="SBB"){
                            searchData[i].trSourceType="";
                        }else if(searchData[i].trSourceType=="SWB"){
                            searchData[i].trSourceType="";
                        }else if(searchData[i].trSourceType=="BNK"){
                            searchData[i].trSourceType="()";
                        }else if(searchData[i].trSourceType=="CSH"){
                            searchData[i].trSourceType="";
                        }

                        if(searchData[i].status==-1){
                            searchData[i].status="";
                        }else if(searchData[i].status==1){
                            searchData[i].status="";
                        }else if(searchData[i].status==2){
                            searchData[i].status="";
                        }else if(searchData[i].status==3){
                            searchData[i].status="";
                        }

                        if(searchData[i].washedYn==0){
                            searchData[i].washedYn = "";
                        }else{
                            searchData[i].washedYn = "";
                        }

                        //
                        let tempData = searchData[i];
                        this.$axios({
                            method:'get',
                            url:"/apis/member/findMBankByMCode",
                            params: {
                                mCode:searchData[i].mCode,
                                date:new Date().getTime()
                            }
                        })
                        .then(response=>{
                            if(response.data.code){
                                let memberBank = response.data.data.memberBank;
                                let trBankOid = tempData.trBankOid;
                                for(let j in memberBank){
                                    if(memberBank[j].oId==trBankOid){
                                        tempData.bankCode = memberBank[j].bankCode;
                                        tempData.accCode = memberBank[j].accCode;
                                        tempData.accType = memberBank[j].accType;
                                    }
                                }
                            }
                        })
                        //
                        searchData[i].bankCode = tempData.bankCode ;
                        searchData[i].accCode = tempData.accCode ;
                        searchData[i].accType = tempData.accType ;
                    }
                    this.searchData = searchData;
                    this.pageData.currentPage = response.data.data.pageNum,
                    this.pageData.pageSize = response.data.data.pageSize,
                    this.pageData.total = response.data.data.total
                }
                setTimeout(()=>{
                    this.loadingTable = false;
                },200)
            })
        },
       //
        onChangeDate(data) {
            if(data) {
                let month1 = data[0].getMonth()+1<10? "0"+ (data[0].getMonth()+1): data[0].getMonth()+1;
                let month2 = data[1].getMonth()+1<10? "0"+ (data[1].getMonth()+1): data[1].getMonth()+1;
                let day1 = data[0].getDate()<10? "0"+ data[0].getDate(): data[0].getDate();
                let day2 = data[1].getDate()<10? "0"+ data[1].getDate(): data[1].getDate();
                this.form.time[0] = data[0].getFullYear()+'-'+month1+'-'+ day1;
                this.form.time[1] = data[1].getFullYear()+'-'+month2+'-'+ day2;
            }
       },
        //
        exportExcel(dom,title) {  
            if(this.searchData.length==0){
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                });
            }else {
                ToExportExcel(dom,title);       
            }
        },
    },
    created() {
        this.onSearch();
    }
};
</script>

<style scoped>
</style>



