<template>
    <el-form :model="form" label-width="80px" label-position="left">

        <el-form-item label="">
            <el-button type="primary" icon="el-icon-search"  @click="onSearch"></el-button>
        </el-form-item>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.mCode" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.nickname" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row> 

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.name" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.idCode" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>    

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.tel" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>   

        <el-row>
            <el-col :span="4">
                <el-form-item label="">
                    <el-input v-model="form.mState" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="4" style="margin-left:-40px;" v-show="form.mCode!=''">
                <el-form-item>
                    <el-button type="success" :disabled="form.mState==''" size="mini" @click="DialogConfirm(0,'')"> </el-button>
                    <el-button type="warning" :disabled="form.mState==''"  size="mini" @click="DialogConfirm(0,'')"> </el-button>
                    <el-button type="danger" :disabled="form.mState==''" size="mini" @click="DialogConfirm(0,'')"> </el-button>
                </el-form-item>
            </el-col>
        </el-row>  

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-button type="primary" @click="onReset"> </el-button>
                </el-form-item>
            </el-col>
        </el-row>   

        <el-row>
            <el-col :span="4">
                <el-form-item label="">
                    <el-input v-model="form.integralState" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="4" style="margin-left:-40px;" v-show="form.mCode!=''">
                <el-form-item>
                    <el-button type="success" :disabled="form.integralState==''"  size="mini" @click="DialogConfirm(1,'')"> </el-button>
                    <el-button type="warning" :disabled="form.integralState==''"  size="mini" @click="DialogConfirm(1,'')"> </el-button>
                    <el-button type="danger" :disabled="form.integralState==''"  size="mini" @click="DialogConfirm(1,'')"> </el-button>
                </el-form-item>
            </el-col>
        </el-row>  

        <el-row>
            <el-col :span="4">
                <el-form-item label="">
                    <el-input v-model="form.rewardIntegral" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="4" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.shopIntegral" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="4" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.changeIntegral" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>  

        <el-row>
            <el-col :span="6">
                <el-form-item label="" label-width="100px">
                    <el-button type="primary" @click="showDialogChange"> </el-button>
                </el-form-item>
            </el-col>
        </el-row>   


        <!--  -->
        <dialog-com ref="dialog" @searchData="getSearchData"></dialog-com>

        <!--  -->
        <el-dialog title="" :visible.sync="DialogState" width="400px" center>
            <p><b>{{stateForm.title}}</b></p>
            <br/>  
            <el-row>
                <el-col :span="24">
                    <el-form-item label=":" label-width="75px">
                        <el-input type="textarea" v-model="stateForm.desc" :autosize="{ minRows: 3, maxRows: 5}"></el-input>
                    </el-form-item>
                </el-col>
            </el-row> 
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="DialogState = false"> </el-button>
                <el-button type="primary" @click="onConfirm"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="DialogPassWord" width="400px" center>
            <el-form status-icon :rules="rules" :model="formPsd"  ref="formPsd" label-width="90px" label-position="left">
                <el-row>
                    <el-col :span="24">
                        <el-form-item label=":" prop="password1">
                            <el-input type="password" v-model="formPsd.password1"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="24">
                        <el-form-item label=":" prop="password2">
                            <el-input type="password" v-model="formPsd.password2"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row> 
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="DialogPassWord = false"> </el-button>
                <el-button type="primary" @click="onChangePassWord('formPsd')"> </el-button>
            </span>
        </el-dialog>
    </el-form>
</template>


<script>
export default {
    data() {
        //
        var validatePass1 = (rule, value, callback) => {
            const reg = /^[0-9a-zA-Z_#]{6,16}$/;
            let leng = reg.test(value);
            if (!leng) {
                callback(new Error('6-16,_#'));
            } else {
                callback();
            }
        };
        var validatePass2 = (rule, value, callback) => {
            if (this.formPsd.password1!=value) {
                callback(new Error('!'));
            } else {
                callback();
            }
        };
        return {
            DialogState:false, //
            DialogPassWord:false, //
            form: {
                mCode: "", //
                name: "", //
                nickname: "", //
                idCode:"", //
                tel:"", //
                mState: "", //
                integralState: "", //
                rewardIntegral: "", //
                shopIntegral: "", //
                changeIntegral: "" //
            },
            stateForm: {
                title:"", //
                state:"", //
                type:"", //
                statusAfter:null, //
                desc:"", //
            },
            formPsd: {
                password1:"", //1
                password2:"", //2
            },
            //
            rules: {
                password1: [
                    { validator: validatePass1, trigger: ['blur','change'] }
                ],
                password2: [
                    { validator: validatePass2, trigger: ['blur','change'] }
                ]
            }
        };
    },
    methods: { 
        //
        onSearch() {
            this.$refs.dialog.onSearchDialog();
        },
        //
        DialogConfirm(val,title) {
            this.stateForm.statusAfter = null;
            this.stateForm.desc = "";
            this.DialogState = true;
            this.stateForm.state = val;
            this.stateForm.type = title;
            //
            if(this.stateForm.state==0){
                if(title==""){
                    this.stateForm.title = "?"
                    this.stateForm.statusAfter = 1;
                }else if(title==""){
                    this.stateForm.title = "?"
                    this.stateForm.statusAfter = 0;
                }else if(title==""){
                    this.stateForm.title = "?"
                    this.stateForm.statusAfter = 2;
                }else{}
            }else{  
                //
                if(title==""){
                    this.title = "?"
                    this.stateForm.statusAfter = 1;
                }else if(title==""){
                    this.title = "?"
                    this.stateForm.statusAfter = 0;
                }else if(title==""){
                    this.title = "?"
                    this.stateForm.statusAfter = 2;
                }else{}
            }
        },
        //
        onConfirm() {
            //
            let statusBefore = null;
            if(this.stateForm.state==0){
                statusBefore = this.form.mState==""?2:(this.form.mState==""?0:1);
            }else{
                //
                statusBefore = this.form.integralState==""?0:(this.form.integralState==""?1:2);
            }
            this.$axios({
                method:'post',
                url:"/apis/member/updateStatusByMCode",
                params:{
                    mCode:this.form.mCode,
                    mNickname:this.form.nickname,
                    statusType:this.stateForm.state==0?"MM":"MR",
                    statusBefore:statusBefore,
                    statusAfter:this.stateForm.statusAfter,
                    updateDesc:this.stateForm.desc
                }
            })     
            .then(response=>{
                if(response.data.code){
                    this.$message({
                        showClose: true,
                        message: this.stateForm.type+'',
                        type: 'success'
                    });
                }else {
                    this.$message({
                    showClose: true,
                    message: response.data.msg,
                    type: 'error'
                });
                }
            })
            setTimeout(()=>{
                this.DialogState = false;
            },200)
        },
        //
        onReset() {
            if(!this.form.mCode) {     //
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });       
            }else{
                this.$confirm('?', '', {
                    confirmButtonText: ' ',
                    cancelButtonText: ' ',
                    type: 'warning',
                    center: true
                }).then(() => {
                    // this.$axios({
                    //     method:'get',
                    //     url:"/apis/member/delMemAddByAId",
                    //     params: {
                    //         aId:row.id,
                    //         mCode:this.form.id
                    //     }
                    // })
                    // .then(response=>{
                    //     if(response.data.code){
                    //         this.$message({
                    //             message: '!',
                    //             type: 'success'
                    //         });
                    //         this.getAddressList();
                    //     } else{
                    //         this.$message({
                    //             showClose: true,
                    //             message: '!',
                    //             type: 'error'
                    //         });
                    //     }
                    // })    
                }).catch(() => {});
            }
        },
        //
        showDialogChange(){
            if(!this.form.mCode) {     //
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });       
            }else{
                this.DialogPassWord = true;
            }
        },
        //
        onChangePassWord(form) {
            this.$refs[form].validate((valid) => {
                if (valid) {

                }else {
                    this.$message({
                        showClose: true,
                        message: '!',
                        type: 'error'
                    }); 
                    return false;
                }
            })
        },
        //
        getSearchData(data) {
            this.form= {
                mCode: data.mCode, //
                name: data.mName, //
                nickname: data.mNickname, //
                idCode:data.idCode, //
                tel:data.mobile, //
                mState: data.mStatus, //
                integralState: "", //
                rewardIntegral: "", //
                shopIntegral: "", //
                changeIntegral: "" //
            }

            this.$axios({
                method:'get',
                url:"/apis/member/findMemAccountByMCode",
                params:{
                    mCode:this.form.mCode,
                    date:new Date().getTime()
                }
            })     
            .then(response=>{
                if(response.data.code){
                    this.form.integralState = response.data.data.bonusStatus==0?"":(response.data.data.bonusStatus==1?"":"");
                    this.form.rewardIntegral = response.data.data.bonusBlance;
                    this.form.shopIntegral = response.data.data.walletBlance;
                    this.form.changeIntegral = response.data.data.redemptionBlance;
                }
            })
        }
    }
};
</script>

<style>
</style>



