<template>
    <el-form ref="form" :rules="rules" :model="form" label-width="110px" label-position="left">

        
        <el-row>
            <el-col :span="9">
                <el-form-item label="">
                    <el-button type="primary" icon="el-icon-search"  @click="onSearch"></el-button>
                </el-form-item>
            </el-col>
            <el-col :span="4">
                <el-button type="success" @click="onAllBing"></el-button>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-col :span="18">
                        <el-input v-model="form.id" disabled></el-input>
                    </el-col>
                    <el-col :span="4" :offset="1">
                        <el-tag type="danger" size="small" v-if="!form.oldId" color="transparent"></el-tag>
                        <el-tag type="danger" size="small" v-if="form.oldId" color="transparent"></el-tag>
                    </el-col>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.name" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.userId" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.tel" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <!--  -->
        <div v-show="form.oldId">
            <el-row>
                <el-col :span="6">
                    <el-form-item label="">
                        <el-input v-model="form.oldId" disabled></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6" :offset="1">
                    <el-form-item label="">
                        <el-input v-model="form.oldName" disabled></el-input>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-row>
                <el-col :span="6">
                    <el-form-item label="">
                        <el-input v-model="form.olduserId" disabled></el-input>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-row>
                <el-col :span="6">
                    <el-form-item label=":" class="serch-input" prop="newId">
                        <el-input  placeholder="" v-model="form.newId"></el-input>
                        <i class="el-icon-search" @click="onSearchInput"></i>
                    </el-form-item>
                </el-col>
                <el-col :span="6" :offset="1">
                    <el-form-item label="" prop="newName">
                        <el-input v-model="form.newName"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-row>
                <el-col :span="6">
                    <el-form-item label="">
                        <el-input v-model="form.newUserId" disabled></el-input>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-row>
                <el-col :span="6">
                    <el-form-item label="" prop="desc">
                        <el-input type="textarea" v-model="form.desc"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
        </div>

        <!--  -->
        <div v-show="!form.oldId">
            <el-row>
                <el-col :span="6">
                    <el-form-item label="">
                        <el-input v-model="form.oldId" disabled></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6" :offset="1">
                    <el-form-item label="">
                        <el-input v-model="form.oldName" disabled></el-input>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-row>
                <el-col :span="6">
                    <el-form-item label=":" class="serch-input" prop="newId">
                        <el-input  placeholder="" v-model="form.newId"></el-input>
                        <i class="el-icon-search" @click="onSearchInput"></i>
                    </el-form-item>
                </el-col>
                <el-col :span="6" :offset="1">
                    <el-form-item label="" prop="newName">
                        <el-input v-model="form.newName"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-row>
                <el-col :span="6">
                    <el-form-item label="">
                        <el-input v-model="form.newUserId" disabled></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6" :offset="1">
                    <el-form-item label="" label-width="160px">
                        <el-input v-model="form.newUserId" disabled></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
        </div>

        <el-form-item>
            <el-button type="primary" :loading="submitLoading" @click="onSubmit('form')"></el-button>
        </el-form-item>

        <!--  -->
        <dialog-com ref="dialog" @searchData="getSearchData"></dialog-com>
    </el-form>
</template>


<script>
export default {
    data() {
        return {
            submitLoading:false,  //loading
            form: {
                id: "", //
                name: "", //
                userId:"", //
                tel:"",     //
                oldId:"",   //
                oldName:"",     //
                olduserId:"",   //
                newId:"",   //
                newName:"",     //
                newUserId:"",     //
                newUserType:"",     //
                desc: "", //
                RefereeId:"", //
                RefereeName:"", //
                RefereeCode:"", //
            },
            //
            rules:{
                newId: [
                    { required: true, message: "", trigger: ['blur','change'] },
                ],
                newName: [
                    { required: true, message: "", trigger: ['blur','change'] },
                ],
                desc: [
                    { required: true, message: "", trigger: ['blur','change'] },
                ]
            }
        };
    },
    methods: {
        //
        onAllBing() {
            if(!this.form.id) {     //
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });   
            }else {
                this.$router.push({
                    name: ``,
                    query: {
                        code: this.form.id
                    }
                })
            }
        },
        //
        onSubmit(form) {
            if(!this.form.id) {     //
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });       
            }else {
                this.$refs[form].validate((valid) => {
                    if (valid) {
                        this.submitLoading = true;
                        if(this.form.oldId) {
                            this.$axios({
                                method:'post',
                                url:"/apis/member/updateBindingByMCode",
                                params: {
                                    mCode:this.form.id,
                                    mName:this.form.name,
                                    raCode:this.form.oldId, 
                                    raName:this.form.oldName,
                                    raCodeNew:this.form.newId,
                                    raNameNew:this.form.newName,
                                    raIdCode:this.form.newUserId,
                                    raIdType:this.form.newUserType,
                                    mDesc:this.form.desc
                                }
                            })
                            .then(response=>{
                                if(response.data.code){
                                    this.$refs.dialog.userDefined({
                                        icon:"success",
                                        title:",!"
                                    });
                                } else{
                                    this.$refs.dialog.userDefined({
                                        icon:"error",
                                        title:response.data.msg
                                    });
                                }
                                this.submitLoading = false;
                            })   
                        }else {
                            this.$axios({
                                method:'post',
                                url:"/apis/member/addBinding",
                                params: {
                                    mCode:this.form.id,
                                    rasponsorCode:this.form.RefereeId,
                                    rasponsorIdCode:this.form.RefereeCode,
                                    rasponsorName:this.form.RefereeName,
                                    raCode:this.form.newId,
                                    raName:this.form.newName,
                                    raIdType:this.form.newUserType,
                                    raIdCode:this.form.newUserId,
                                }
                            })
                            .then(response=>{
                                if(response.data.code){
                                    this.$refs.dialog.userDefined({
                                        icon:"success",
                                        title:",!"
                                    });
                                } else{
                                    this.$refs.dialog.userDefined({
                                        icon:"error",
                                        title:response.data.msg
                                    });
                                }
                                this.submitLoading = false;
                            }) 
                        }
                    } else {
                        this.$message({
                            showClose: true,
                            message: '!',
                            type: 'error'
                        });
                        return false;
                    }
                }); 
            }
        },
        //
        onSearch() {
            this.$refs.dialog.onSearchDialog();
        },
        //
        onSearchInput() {
            if(!this.form.id) {     //
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });       
            }else{
                this.isInputSearch = true;
                this.$refs.dialog.onSearchDialog(this.form.newRefereeId);

            }
        },
         //
        getMemberInfo() {
            this.$axios({
                method:'get',
                url:"/apis/member/findBindingByMCode",
                params: {
                    mCode:this.form.id
                }
            })
            .then(response=>{
                if(response.data.code){
                    this.form.olduserId = response.data.data.rdRaBinding.raIdCode;
                }
            })    
        },
        //
        getSearchData(data) {
            if(this.isInputSearch) {
                this.form.newId = data.mCode;
                this.form.newName = data.mName;
                this.form.newUserId = data.idCode;
                this.form.newUserType = data.idType;
            }else {
                this.form.oldId ="";
                this.form.oldName = "";
                this.form.olduserId = "";
                this.form.newId = "";
                this.form.newName = "";
                this.form.newUserId = "";
                this.form.newUserType = "";
                this.form.desc = "";
                this.form.RefereeId = "";
                this.form.RefereeName = "";
                this.form.RefereeCode = "";
    
                this.form.id = data.mCode;
                this.form.name = data.mName;
                this.form.userId = data.idCode;
                this.form.tel = data.mobile;
                this.form.oldId = data.refereeId;
                this.form.oldName = data.refereeName;
                this.getMemberInfo();
            }
        }
    }
};
</script>

<style>
.wrap .serch-input .el-form-item__content {
    position: relative;
}
.wrap .serch-input .el-icon-search {
    position: absolute;
    right: 10px;
    top: 12px;
    font-size: 18px;
    color: #666;
    cursor: pointer;
}
.wrap .el-tag--danger {
    border:none;
}
</style>



