<template>
    <div class="wrap">
        <!--  -->
        <div class="line">
            <span></span>
            <el-form ref="form" :model="searchFrom" label-width="70px" label-position="left">
                <el-row>
                    <el-col :span="5">
                        <el-col :span="13">
                            <el-form-item label="" label-width="70px">
                                <el-select v-model="searchFrom.id" placeholder="">
                                    <el-option v-for="(items,index) in selectData1" :key="index" :label="items" :value="items"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="10" :offset="1">
                            <el-input v-model="searchFrom.inputId"></el-input>
                        </el-col>
                    </el-col>

                    <el-col :span="5" :offset="1">
                        <el-col :span="13">
                            <el-form-item label="">
                                <el-select v-model="searchFrom.nickname" placeholder="">
                                    <el-option v-for="(items,index) in selectData1" :key="index" :label="items" :value="items"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="10" :offset="1">
                            <el-input v-model="searchFrom.inputNickName"></el-input>
                        </el-col>
                    </el-col>

                    <el-col :span="5" :offset="1">
                        <el-col :span="13">
                            <el-form-item label="">
                                <el-select v-model="searchFrom.name" placeholder="">
                                    <el-option v-for="(items,index) in selectData1" :key="index" :label="items" :value="items"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="10" :offset="1">
                            <el-input v-model="searchFrom.inputName"></el-input>
                        </el-col>
                    </el-col>

                    <el-col :span="5" :offset="1">
                        <el-col :span="13">
                            <el-form-item label="" label-width="70px">
                                <el-select v-model="searchFrom.tel" placeholder="">
                                    <el-option v-for="(items,index) in selectData1" :key="index" :label="items" :value="items"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="10" :offset="1">
                            <el-input v-model="searchFrom.inputTel"></el-input>
                        </el-col>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="5">
                        <el-col :span="13">
                            <el-form-item label="">
                                <el-select v-model="searchFrom.grId" placeholder="">
                                    <el-option v-for="(items,index) in selectData1" :key="index" :label="items" :value="items"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="10" :offset="1">
                            <el-input v-model="searchFrom.inputGrId"></el-input>
                        </el-col>
                    </el-col>
                    <el-col :span="5" :offset="1">
                        <el-col :span="13">
                            <el-form-item label="">
                                <el-select v-model="searchFrom.grname" placeholder="">
                                    <el-option v-for="(items,index) in selectData1" :key="index" :label="items" :value="items"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="10" :offset="1">
                            <el-input v-model="searchFrom.inputGrName"></el-input>
                        </el-col>
                    </el-col>
                    <el-col :span="5" :offset="1">                       
                        <el-col :span="12">
                            <el-form-item label="" label-width="50px" class="text-center">
                                <el-select v-model="searchFrom.levelFrom" placeholder="">
                                    <el-option v-for="(items,index) in selectData3" :key="index" :label="items" :value="items"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="" label-width="50px" class="text-center">
                                <el-select v-model="searchFrom.levelTo" placeholder="">
                                    <el-option v-for="(items,index) in selectData3" :key="index" :label="items" :value="items"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-col>
                    <el-col :span="5" :offset="1">
                        <el-col :span="13">
                            <el-form-item label="">
                                <el-select v-model="searchFrom.ostatus" placeholder="">
                                    <el-option v-for="(items,index) in selectData2" :key="index" :label="items" :value="items"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="24">
                        <el-form-item label="">
                            <el-date-picker 
                                v-model="searchFrom.joioTime" 
                                type="daterange" 
                                range-separator="-"
                                start-placeholder=""
                                end-placeholder=""
                                value-format="yyyy-MM-dd-HH:mm:ss"
                                :default-time="['00:00:00', '23:59:59']"
                                :picker-options="pickerOptions">
                            </el-date-picker>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
        </div>
        <!-- ||  -->
        <el-row>
            <el-col :span="8" >
                <el-pagination
                    :page-size="pageData.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :page-sizes="[2 ,10, 30, 50,pageData.total]"
                    :total="pageData.total"
                    :current-page="pageData.currentPage"
                    @current-change="onChangePage"  
                    @size-change="handleSizeChange">
                </el-pagination>
            </el-col>
            <el-col :span="12"> 
                <el-col :span="3"><el-button size="small" type="primary" @click="onSearch"></el-button></el-col>
                <el-col :span="3"><el-button size="small" @click="onDetail"></el-button></el-col>
                <el-col :span="3"><el-button size="small" type="success" @click="exportExcel('#memberTable','')"></el-button></el-col>       
            </el-col>
        </el-row>
        <br/>

        <!--  -->
        <el-table 
            ref="multipleTable" 
            id="memberTable"
            :data="tableData" 
            tooltip-effect="dark" 
            border
            v-loading="loadingTable" 
            element-loading-text=""
            element-loading-spinner="el-icon-loading">
            <el-table-column label="" type="" width="50" align="center">
                <template slot-scope="scope">
                    <el-radio class="radio" v-model="selectMember" :label="scope.row" @change.native="getCurrentRow(scope.row)">&nbsp;</el-radio>
                </template>
            </el-table-column>
            <el-table-column prop="mCode" label="" width="80" align="center" sortable>                   
            </el-table-column>
            <el-table-column prop="mName" label="" width="70" align="center">
            </el-table-column>
            <el-table-column prop="mNickname" label="" align="center">
            </el-table-column>
                <el-table-column prop="refereeId" label="" align="center" width="100">
            </el-table-column>
                <el-table-column prop="refereeName" label="" align="center" width="100">
            </el-table-column>
                <el-table-column prop="mobile" label="" align="center" width="100">
            </el-table-column>
                <el-table-column label="" width="50" align="center">
                    <template slot-scope="scope">
                        {{scope.row.gender==0?'':''}}
                </template>
            </el-table-column>
                <el-table-column prop="birthdate" label="" align="center" width="90">
            </el-table-column> 
            <el-table-column prop="creationData" label="" align="center" width="90">
            </el-table-column>
            <el-table-column prop="updateDate" label="" align="center" width="90">
            </el-table-column>
            <el-table-column prop="mLevel" label="" align="center" width="90">
            </el-table-column>
            <el-table-column prop="mStatus" label="" align="center" width="50">
            </el-table-column>
            <el-table-column prop="province" label="" align="center">
            </el-table-column>
            <el-table-column prop="city" label="" align="center">
            </el-table-column>
            <el-table-column prop="country" label="" align="center">
            </el-table-column>
            <el-table-column prop="detial" label="" align="center" width="200">
            </el-table-column>
            <el-table-column prop="addPost" label="" width="65" align="center">
            </el-table-column>
        </el-table>
    </div>
</template>


<script>
import Vue from 'vue'
import {ToExportExcel} from "../../util/util.js";
export default {
    data() {
        return {
            loadingTable:false, //
            //
            selectData1: [
                "...",
                "",
                "",
                "",
                "",
                "",
                ""
            ],
            selectData2: ["", "", "", ""],
            selectData3: [
                "",
                "",
                "vip",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                ""
            ],
            //
            searchFrom: {
                id: "", //
                nickname: "", //
                name: "", //
                tel: "", //
                grId: "", //
                grname: "", //
                ostatus: "", //
                joioTime: "", //
                levelFrom: "", //from
                levelTo: "", //to
                inputId: "", //
                inputNickName: "", //
                inputName: "", //
                inputTel: "", //
                inputGrId: "", //
                inputGrName: "" //
            },
            //
            pageData:{
                currentPage:1,
                pageSize:2,
                total:null,
            },
            selectMember:"",   //
            //
            tableData: [],
            //
            pickerOptions: {
                shortcuts: [{
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                    }
                },
                {
                    text: '',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                        picker.$emit('pick', [start, end]);
                    }
                }, 
                {
                    text: '',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                        picker.$emit('pick', [start, end]);
                    }
                }]
            }
        };
    },
    methods: {
        //
        onChangePage(currentPage) {
            this.pageData.currentPage = currentPage;
            this.getMemberinfo();
        },
        //
        handleSizeChange(pageSize) {
            this.pageData.pageSize = pageSize;
            this.getMemberinfo();
        },
        //
        onSearch() {
            //
            let term = this.formSearch;
            if(term.inputId!=""||term.inputNickName!=""||term.inputName!=""||term.inputTel!=""||term.inputGrId!=""||term.inputGrName!=""){
                //
                this.$axios.get("/apis/member/findByMCode", {
                    params:{
                        mCode:this.formSearch.inputId,
                    }
                })
                .then(response=>{
                    if(!response.data.data){
                        //
                        util.$emit("userDefined",{
                                icon:"success",
                                title:""
                            });
                    }else {
                        this.searchData.push(response.data.data);
                    }
                })
            } else{
                //
                util.$emit("userDefined",{
                    icon:"error",
                    title:response.data.msg
                });
    
            }
            
        },
        //
        exportExcel(dom,title) {  
            if(this.tableData.length==0){
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                });
            }else {
                ToExportExcel(dom,title);       
            }
        },
        //
        getMemberinfo() {
            this.loadingTable = true; 
            this.$axios({
                method:'post',
                url:"/apis/member/search",
                params:{
                    currentPage:this.pageData.currentPage,
                    pageSize:this.pageData.pageSize,
                    mCode:this.searchFrom.mCode,
                    mName:this.searchFrom.mName,
                    mobile:this.searchFrom.mobile,
                    mNickname:this.searchFrom.mNickname,
                    date:new Date().getTime()
                }
            })     
            .then(response=>{
                this.tableData = response.data.data.list;
                this.pageData.total = response.data.data.total;
                for(let i = 0; i < response.data.data.list.length; i++){
                    //
                    this.tableData[i].birthdate = this.tableData[i].birthdate.slice(0,10);
                    //
                    this.$axios({
                        method:'get',
                        url:"/apis/member/findRelationByMCode",
                        params:{
                            mCode:response.data.data.list[i].mCode,
                            date:new Date().getTime()
                        }
                    })     
                    .then(response=>{ 
                        if(response.data.code){
                            this.tableData[i].mStatus = response.data.data.memberRelation.rank==1?'':(response.data.data.memberRelation.rank==2?'':'');
                            this.tableData[i].mLevel = response.data.data.rankName;
                            Vue.set(this.tableData,i,this.tableData[i])
                        }
                    })
                    //
                    this.$axios({
                        method:'get',
                        url:"/apis/member/findRelationByMCode",
                        params: {
                            mCode:response.data.data.list[i].mCode,
                            date:new Date().getTime()
                        }
                    })
                    .then(response=>{
                        if(response.data.code){
                            this.tableData[i].refereeId = response.data.data.memberRelation.sponsorCode;
                            this.tableData[i].refereeName = response.data.data.memberRelation.sponsorName;
                            Vue.set(this.tableData,i,this.tableData[i])
                        }
                    })   
                    
                }
                setTimeout(()=>{
                    this.loadingTable = false;
                },200)
            })
        },
        //
        getCurrentRow(row){
            this.selectMember = row;
        },
        //
        onDetail() {
            if(!this.selectMember){
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });
            }else {
                this.$router.push({
                    path:"/mdetailed",
                    params:{
                        mCode:this.selectMember.mCode
                    }
                })
            }
        }
    },
    created () {
        this.getMemberinfo();
    }
};
</script>

<style>
.line {
    width: 100%;
    position: relative;
    display: inline-block;
    border: 1px solid #888;
    border-radius: 10px;
    padding: 20px;
    margin-bottom: 30px;
}

.line > span {
    position: absolute;
    top: -10px;
    padding: 0 10px;
    background: #fff;
}
.line .el-input__inner {
    margin-top: 7px;
}
.serch-input .el-form-item__content {
    position: relative;
}
.serch-input .el-icon-search {
    position: absolute;
    right: 10px;
    top: 12px;
    font-size: 18px;
    color: #666;
    cursor: pointer;
}
.el-icon-search:hover {
    color: #409eff;
}
.area-select.medium {
    width: 90px;
}
.text-center .el-form-item__label {
    text-align: center;
}
.text-center .long-input {
    min-width: 400px;
}
.number-count {
    padding: 0 10px;
}
.btn-center .el-form-item__content {
    display: flex;
    justify-content: space-around;
}
</style>



