<template>
    <el-form  label-width="80px" label-position="left">
        <el-row type="flex" justify="space-between">
            <el-col :span="14" class="top-title">
                <span><b>{{periodCode}}</b></span>  
                <span>{{number}}</span>
            </el-col>
            <el-col :span="4" align="right">
                <el-button type="primary"  @click="onCount" :disabled="submitDisable"> </el-button>
            </el-col>
        </el-row>
        <br>
        <el-row>
            <el-col :span="24">
                <el-table 
                    :data="tableData" 
                    v-loading="loadingTable" 
                    element-loading-text=","
                    element-loading-spinner="el-icon-loading">
                    <el-table-column prop="mCode" fixed label="" align="center">
                    </el-table-column>
                    <el-table-column prop="mName" label="" align="center"> 
                    </el-table-column>
                    <el-table-column prop="ppv" label="PV" align="center" width="140px">
                    </el-table-column>
                    <el-table-column prop="ppvRetail" label="PV" align="center" width="140px">
                    </el-table-column>
                    <el-table-column prop="appvInit" label="PV" align="center" width="140px">
                    </el-table-column>
                    <el-table-column prop="appvFinal" label="PV"  align="center" width="140px">
                    </el-table-column>
                    <el-table-column prop="ppvqualified" label="" align="center" width="140px">
                    </el-table-column>
                    <el-table-column prop="rankInit" label="" align="center" width="130px">
                    </el-table-column>
                    <el-table-column prop="rank" label="" align="center" width="140px">
                    </el-table-column>
                    <el-table-column prop="rankRecordHigh" label="" align="center" width="130px">
                    </el-table-column>
                    <el-table-column prop="layer" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="leafYn" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="g7pv" label="G7PV" align="center">
                    </el-table-column>
                    <el-table-column prop="npv" label="NPV" align="center">
                    </el-table-column>
                    <el-table-column prop="gpvFlagship" label="GPV" align="center">
                    </el-table-column>
                    <el-table-column prop="groupRankMax" label="" align="center" width="140px">
                    </el-table-column>
                    <el-table-column prop="ddRank2Number" label="" align="center" width="140px">
                    </el-table-column>
                    <el-table-column prop="dlRank6Number" label="3" align="center" width="160px">
                    </el-table-column>
                    <el-table-column prop="dlRank7Number" label="" align="center" width="140px">
                    </el-table-column>
                    <el-table-column prop="dlRank8Number" label="" align="center" width="160px">
                    </el-table-column>
                    <el-table-column prop="tourismQualified" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="carQualified" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="dividendQualified" label="" align="center">
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <br>
        <el-row type="flex" justify="center">
            <el-col :span="8" align="center">
                <el-pagination
                    :page-size="pageData.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :page-sizes="[10, 20, 30, 50,pageData.total]"
                    :total="pageData.total"
                    :current-page="pageData.currentPage"
                    @current-change="onChangePage"  
                    @size-change="handleSizeChange">
                </el-pagination>
            </el-col>
        </el-row>
    </el-form>
</template>


<script>
export default {
    data() {
        return {
            submitDisable:false, 
            loadingTable:false, //
            periodCode:"",  //
            number:0,   //
            form: {
                id: "", //
                name: "", //
            },
            //
            tableData: [],
            //
            pageData:{
                currentPage:1,
                pageSize:10,
                total:null,
            },
        };
    },
    methods: {
        //
        onSearch() {
            this.loadingTable = true;  
            this.$axios({
                method:'post',
                url:"/apis/member/findQualificationAll",
                params:{
                    periodCode:this.periodCode,
                    currentPage:this.pageData.currentPage,
                    pageSize:this.pageData.pageSize,
                    date:new Date().getTime()
                }
            })     
            .then(response=>{
                if(response.data.code){ 
                    this.tableData = response.data.data.list;
                    this.pageData.total = response.data.data.total;
                    this.pageData.currentPage = response.data.data.pageNum;
                    this.pageData.pageSize = response.data.data.pageSize;
                    this.tableData = response.data.data.list;
                    for(var i in this.tableData){
                        this.number++;
                        //
                        if(this.tableData[i].rankInit==0){
                            this.tableData[i].rankInit = "";
                        }else if(this.tableData[i].rankInit==1){
                            this.tableData[i].rankInit = "VIP";
                        }else if(this.tableData[i].rankInit==2){
                            this.tableData[i].rankInit = "";
                        }else if(this.tableData[i].rankInit==3){
                            this.tableData[i].rankInit = "";
                        }else if(this.tableData[i].rankInit==4){
                            this.tableData[i].rankInit = "";
                        }else if(this.tableData[i].rankInit==5){
                            this.tableData[i].rankInit = "";
                        }else if(this.tableData[i].rankInit==6){
                            this.tableData[i].rankInit = "";
                        }else if(this.tableData[i].rankInit==7){
                            this.tableData[i].rankInit = "";
                        }else if(this.tableData[i].rankInit==8){
                            this.tableData[i].rankInit = "";
                        }else if(this.tableData[i].rankInit==9){
                            this.tableData[i].rankInit = "";
                        }
                        //
                        if(this.tableData[i].rank==0){
                            this.tableData[i].rank = "";
                        }else if(this.tableData[i].rank==1){
                            this.tableData[i].rank = "VIP";
                        }else if(this.tableData[i].rank==2){
                            this.tableData[i].rank = "";
                        }else if(this.tableData[i].rank==3){
                            this.tableData[i].rank = "";
                        }else if(this.tableData[i].rank==4){
                            this.tableData[i].rank = "";
                        }else if(this.tableData[i].rank==5){
                            this.tableData[i].rank = "";
                        }else if(this.tableData[i].rank==6){
                            this.tableData[i].rank = "";
                        }else if(this.tableData[i].rank==7){
                            this.tableData[i].rank = "";
                        }else if(this.tableData[i].rank==8){
                            this.tableData[i].rank = "";
                        }else if(this.tableData[i].rank==9){
                            this.tableData[i].rank = "";
                        }
                        //
                        if(this.tableData[i].rankRecordHigh==0){
                            this.tableData[i].rankRecordHigh = "";
                        }else if(this.tableData[i].rankRecordHigh==1){
                            this.tableData[i].rankRecordHigh = "VIP";
                        }else if(this.tableData[i].rankRecordHigh==2){
                            this.tableData[i].rankRecordHigh = "";
                        }else if(this.tableData[i].rankRecordHigh==3){
                            this.tableData[i].rankRecordHigh = "";
                        }else if(this.tableData[i].rankRecordHigh==4){
                            this.tableData[i].rankRecordHigh = "";
                        }else if(this.tableData[i].rankRecordHigh==5){
                            this.tableData[i].rankRecordHigh = "";
                        }else if(this.tableData[i].rankRecordHigh==6){
                            this.tableData[i].rankRecordHigh = "";
                        }else if(this.tableData[i].rankRecordHigh==7){
                            this.tableData[i].rankRecordHigh = "";
                        }else if(this.tableData[i].rankRecordHigh==8){
                            this.tableData[i].rankRecordHigh = "";
                        }else if(this.tableData[i].rankRecordHigh==9){
                            this.tableData[i].rankRecordHigh = "";
                        }
                        //
                        if(this.tableData[i].ppvqualified==0){
                            this.tableData[i].ppvqualified = "";
                        }else if(this.tableData[i].ppvqualified==1){
                            this.tableData[i].ppvqualified = "";
                        }
                        //
                        if(this.tableData[i].leafYn==1){
                            this.tableData[i].leafYn = "";
                        }else{
                            this.tableData[i].leafYn = "";
                        }   
                        //
                        if(this.tableData[i].tourismQualified==0){
                            this.tableData[i].tourismQualified = "";
                        }else{
                            this.tableData[i].tourismQualified = "";
                        }   
                        //
                        if(this.tableData[i].carQualified==0){
                            this.tableData[i].carQualified = "";
                        }else{
                            this.tableData[i].carQualified = "";
                        }   
                        //
                        if(this.tableData[i].dividendQualified==0){
                            this.tableData[i].dividendQualified = "";
                        }else{
                            this.tableData[i].dividendQualified = "";
                        }   
                    }
                }
                setTimeout(()=>{
                    this.loadingTable = false;
                },200)
            })
        },
        //
        onCount() { 
            this.submitDisable = true; 
            this.loadingTable = true;
            this.$axios({
                method:'get',
                url:"/apis/member/countNowPeriod",
                params:{
                    periodCode:this.periodCode,
                }
            })     
            .then(response=>{
                if(response.data.code) {
                    setTimeout(()=>{
                        this.$message({
                            showClose: true,
                            message: '',
                            type: 'success'
                        });
                        this.onSearch();  
                    },1000)
                }else {
                    this.$message({
                        showClose: true,
                        message: response.data.msg,
                        type: 'error'
                    });
                }
            })
        },
        //
        onChangePage(currentPage) {
            this.form.currentPage = currentPage;
            this.onSearch();
        },
        //
        handleSizeChange(pageSize) {
            this.pageData.pageSize = pageSize;
            this.onSearch();
        }
    },
    created() {
        this.periodCode = this.$route.query.periodCode;
        this.onSearch();
    },
    updated() {
        for(var i in this.tableData){
            if(this.tableData[i].layer > 0){
                this.submitDisable = true; 
            }
        }
    }
};
</script>
<style scoped>
.top-title span{
    display: inline-block;
    margin-right: 30px;
}
</style>




