<template>
    <div class="wrap">

        <!--  -->
        <el-dialog :visible.sync="DialogsearchUser" width="90%" center>
            <el-form label-width="70px" label-position="left">
                <el-row>
                    <el-col :span="6">
                        <el-form-item label="">
                            <el-input v-model="searchFrom.mCode"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6" :offset="1">
                        <el-form-item label="">
                            <el-input v-model="searchFrom.mName"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="3">
                        <el-form-item>
                            <el-button type="primary" @click="onSearchDialog"></el-button>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="6">
                        <el-form-item label="">
                            <el-input v-model="searchFrom.mobile"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6" :offset="1">
                        <el-form-item label="">
                            <el-input v-model="searchFrom.mNickname"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <el-table 
                ref="multipleTable" 
                :data="tableData" 
                tooltip-effect="dark" 
                border
                v-loading="loadingTable" 
                element-loading-text=""
