                <el-table-column label="" type="" width="55" align="center">
                    <template slot-scope="scope">
                        <el-radio class="radio" v-model="selectNum" :label="scope.$index" @change.native="getCurrentRow(scope.$index)">&nbsp;</el-radio>
                    </template>
                </el-table-column>
                <el-table-column prop="mCode" label="" width="80" align="center" sortable>                   
                </el-table-column>
                <el-table-column prop="mName" label="" width="90" align="center">
                </el-table-column>
                <el-table-column prop="mNickname" label="" align="center">
                </el-table-column>
                 <el-table-column prop="refereeId" label="" align="center" width="100">
                </el-table-column>
                 <el-table-column prop="refereeName" label="" align="center" width="100">
                </el-table-column>
                 <el-table-column prop="mobile" label="" align="center" width="100">
                </el-table-column>
                 <el-table-column label="" width="50" align="center">
                     <template slot-scope="scope">
                         {{scope.row.gender==0?'':''}}
                    </template>
                </el-table-column>
                 <el-table-column prop="birthdate" label="" align="center" width="90">
                </el-table-column> 
                <el-table-column prop="creationData" label="" align="center" width="90">
                </el-table-column>
                <el-table-column prop="updateDate" label="" align="center" width="90">
                </el-table-column>
                <el-table-column prop="mLevel" label="" align="center" width="120">
                </el-table-column>
                <el-table-column prop="mStatus" label="" align="center" width="50">
                </el-table-column>
                <el-table-column prop="province" label="" align="center">
                </el-table-column>
                <el-table-column prop="city" label="" align="center">
                </el-table-column>
                <el-table-column prop="country" label="" align="center">
                </el-table-column>
                <el-table-column prop="detial" label="" align="center" width="200">
                </el-table-column>
                <el-table-column prop="addPost" label="" width="70" align="center">
