                            <span :class="{'diff':changeDetails.genderBefore!=changeDetails.gender}">{{changeDetails.gender==0?'':''}}</span>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label=":">
