                    </el-col>
                    <el-col :span="12">
                        <el-form-item label=":">
                           <span :class="{'diff':changeDetails.mobileBefore!=changeDetails.mobile}">{{changeDetails.mobile}}</span>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
