                            {{changeDetails.weChatBefore}}
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label=":">
                            <span :class="{'diff':changeDetails.weChatBefore!=changeDetails.weChat}">{{changeDetails.weChat}}</span>
                        </el-form-item>
