<template>
    <div class="wrap">
        <!--  -->
        <div class="line">
            <span></span>
            <el-form :inline="true" :model="formSearch" class="demo-form-inline">
                <el-form-item label=":" class="serch-input">
                    <el-input v-model="formSearch.id" placeholder=""></el-input>
                    <i class="el-icon-search" @click="searchCode"></i>
                </el-form-item>
                <el-form-item label=":" class="serch-input">
                    <el-input v-model="formSearch.nickname" placeholder=""></el-input>
                    <i class="el-icon-search" @click="onSearch2"></i>
                </el-form-item>
                <el-form-item label=":" class="serch-input">
                    <el-input v-model="formSearch.name" placeholder=""></el-input>
                    <i class="el-icon-search" @click="onSearch3"></i>
                </el-form-item>
            </el-form>
        </div>

        <!--  -->
        <div class="line">
            <span></span>
            <el-form ref="form" :rules="rules" :model="formMember" label-width="90px" label-position="left">
                <el-row>
                    <el-col :span="8">
                        <el-form-item label=":" prop="name">
                            <el-input v-model="formMember.name"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8" :offset="1">
                        <el-form-item label=":" prop="nickname">
                            <el-input v-model="formMember.nickname"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>

                <el-row>
                    <el-col :span="8">
                        <el-form-item label=":">
                            <el-select v-model="formMember.IDType" placeholder="" prop="IDType">
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8" :offset="1">
                        <el-form-item label=":" prop="IDNumber">
                            <el-input v-model="formMember.IDNumber"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>

                <el-row>
                    <el-col :span="6">
                        <el-form-item label=":">
                            <el-radio-group v-model="formMember.sex">
                                <el-radio label=""></el-radio>
                                <el-radio label=""></el-radio>
                                <el-radio label=""></el-radio>
                            </el-radio-group>
                        </el-form-item>
                    </el-col>

                    <el-col :span="5">
                        <el-form-item label=":">
                            <el-date-picker type="date" placeholder=""  value-format="yyyy-MM-dd" v-model="formMember.date"></el-date-picker>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6" :offset="1">
                        <el-form-item label=":" prop="tel">
                            <el-input v-model.number="formMember.tel"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>

                <el-row>
                    <el-col :span="24">
                        <el-form-item label=":" class="inline-block">
                            <div class="area">
                                <area-select type="text" :level="2" :placeholders="placeholders" v-model="formMember.address" :data="pcaa"></area-select>
                            </div>
                        </el-form-item>
                        <el-form-item v-model="formMember.address" label=":" class="text-center inline-block">
                            <el-input class="long-input"></el-input>
                        </el-form-item>
                        <el-form-item label=":" prop="zipCode" class="text-center inline-block">
                            <el-input v-model.number="formMember.zipCode"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>

                <el-row>
                    <el-col :span="7">
                        <el-form-item label="Email:" prop="email">
                            <el-input v-model="formMember.email"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="7" :offset="1">
                        <el-form-item label=":" prop="email">
                            <el-input v-model="formMember.wechat"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="7" :offset="1">
                        <el-form-item label="QQ:" prop="email">
                            <el-input v-model="formMember.qq"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>

                <el-row>
                    <el-col :span="4">
                        <el-form-item label=":" prop="email">
                            <el-select v-model="formMember.accountType" placeholder="">
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="4">
                        <el-form-item label=":" prop="email" label-width="60px" style="text-align:center;">
                            <el-input v-model="formMember.accountName"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6" :offset="1">
                        <el-form-item label=":" prop="email">
                            <el-input v-model="formMember.accountName"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6" :offset="1">
                        <el-form-item label=":" prop="email">
                            <el-input v-model="formMember.accountNumber"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>

                <!--  -->
                <dialog-com></dialog-com>

            </el-form>
        </div>

        <!--  -->
        <div class="line">
            <span></span>
            <el-table :data="tableData" border center>
                <el-table-column prop="date" label="" width="90" align="center">
                </el-table-column>
                <el-table-column prop="name" label="" align="center">
                </el-table-column>
                <el-table-column prop="number" label="" align="center">
                    <template slot-scope="scope">
                        <el-button
                        size="mini"
                        type="primary"
                        plain
                        @click="reduceBtn(scope.$index, scope.row)">-</el-button>
                        <span class="number-count">{{scope.row.number}}</span>
                        <el-button
                        size="mini"
                        type="primary"
                        plain
                        @click="addBtn(scope.$index, scope.row)">+</el-button>
                    </template>
                </el-table-column>
                <el-table-column prop="price" label="" align="center">
                </el-table-column>
                <el-table-column prop="MPrice" label="" align="center">
                </el-table-column>
                <el-table-column prop="money" label="" align="center">
                </el-table-column>
                <el-table-column prop="PV" label="PV" align="center">
                </el-table-column>
                <el-table-column prop="AllPv" label="PV" align="center">
                </el-table-column>
            </el-table>

            <el-form :model="formSearch">
                <el-row>
                    <el-col :span="6">
                        <el-form-item label=":">123</el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label=":">123</el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label=":">123</el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="PV:">123</el-form-item>
                    </el-col>
                </el-row>

                <el-row>
                    <el-col :span="24">
                        <el-form-item label=":">
                            <el-radio-group v-model="formMember.sex">
                                <el-radio label=""></el-radio>
                                <el-radio label=""></el-radio>
                                <el-radio label=""></el-radio>
                                <el-radio label=""></el-radio>
                            </el-radio-group>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>

            <el-form :inline="true" :model="formSearch" class="demo-form-inline">
                <el-row>
                    <el-col :span="24">
                        <el-form-item>
                            <div class="area">
                                <area-select type="text" :level="2" :placeholders="placeholders" v-model="formMember.address" :data="pcaa"></area-select>
                            </div>
                        </el-form-item>                                      
                        <el-form-item label=":" class="text-center">
                            <el-input class="long-input"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <br/>
                <el-row>
                    <el-col :span="6">
                        <el-form-item label=":" class="inline-block">
                            <el-input v-model="formSearch.id" class="inline-block"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label=":" class="inline-block">
                            <el-input v-model="formSearch.nickname" class="inline-block"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>

        </div>
        <!-- |  -->
        <el-form>
            <el-row>
                <el-col :span="24">
                    <el-form-item class="btn-center block">
                        <el-button @click="resetForm"></el-button>
                            <el-button :offset="1" type="primary" @click="onSubmit"></el-button>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>

        <!--  -->
        <el-dialog :visible.sync="searchUser" width="80%" center>
            <el-table 
                ref="multipleTable" 
                :data="formData" 
                tooltip-effect="dark" 
                border 
                size="mini"
                v-loading="loadingTable" 
                element-loading-text=""
                element-loading-spinner="el-icon-loading">
                 <el-table-column label="" type="" width="55">
                    <template slot-scope="scope">
                        <el-radio class="radio" v-model="radio" :label="scope.$index" @change.native="getCurrentRow(scope.$index)">&nbsp;</el-radio>
                    </template>
                </el-table-column>
                <el-table-column prop="mcode" label="" width="90" align="center" sortable>                   
                </el-table-column>
                <el-table-column prop="mname" label="" width="90" align="center">
                </el-table-column>
                <el-table-column prop="mnickname" label="" align="center">
                </el-table-column>
                 <el-table-column prop="mid" label="" align="center" >
                </el-table-column>
                 <el-table-column prop="address" label="" align="center">
                </el-table-column>
                 <el-table-column prop="mobile" label="" align="center" width="140">
                </el-table-column>
                 <el-table-column prop="gender" label="" width="50" align="center">
                </el-table-column>
                 <el-table-column prop="" label="" align="center">
                </el-table-column> 
                <el-table-column prop="" label="" align="center">
                </el-table-column>
                <el-table-column prop="idType" label="" align="center">
                </el-table-column>
                <el-table-column prop="idType" label="" align="center">
                </el-table-column>
                <el-table-column prop="idType" label="" align="center" width="50">
                </el-table-column>
                <el-table-column prop="province" label="" align="center">
                </el-table-column>
                <el-table-column prop="city" label="" align="center">
                </el-table-column>
                <el-table-column prop="country" label="" align="center">
                </el-table-column>
                <el-table-column prop="detial" label="" align="center" width="200">
                </el-table-column>
                <el-table-column prop="addPost" label="" width="70" align="center">
                </el-table-column>
            </el-table>
            <span slot="footer" class="dialog-footer">
                <el-button @click="searchUser = false"> </el-button>
                <el-button type="primary" :disabled="isdisabled" @click="sendData"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <dialog-com></dialog-com>


    </div>
</template>


<script>
import util from "../../../util/util.js";
import { pca, pcaa } from "area-data";
export default {
    data() {
        return {
            placeholders: ["", "", ""],
            pca: pca,
            pcaa: pcaa,
            searchUser: false,  //
            isdisabled:true,  //
            loadingTable:true, //
            radio:"",
            //
            searchFrom:{
                mCode:"",
                mName:"",
                mobile:"",
                mNickname:""
            },
            //
            formSearch: {
                id: "",
                nickname: "",
                name: ""
            },
            //
            formData:[],
            selectNum:"",  //
            //
            formMember: {
                id:"", //
                name: "", //
                nickname: "", //
                IDType: "", //
                IDNumber: "12321321321321321", //
                sex: "", //
                date: "1990-12-12",
                tel: "18807258866", //
                address: [],
                zipCode: "125556", //
                email: "12312321@163.com", //
                wechat: "981665165dsfds", //
                qq: "123213", //qq
                accountType: "", //
                accountName: "", //
                accountNumber: "12321312" //
            },
            //
            rules: {
                name: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                nickname: [
                    { required: true, message: "", trigger: "blur" }
                ],
                IDType: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                IDNumber: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                tel: [
                    {
                        required: true,
                        message: "",
                        trigger: "change"
                    },
                    {
                        type: "number",
                        message: "",
                        trigger: "change"
                    }
                ]
            },
            //
            tableData: [
                {
                    date: "V01001",
                    name: "VIP",
                    number: 1,
                    price: 380.003,
                    MPrice: 300.0,
                    money: 380.0,
                    PV: 0.0,
                    AllPv: 0.0
                },
                {
                    date: "V01001",
                    name: "VIP",
                    number: 1,
                    price: 380.0,
                    MPrice: 300.0,
                    money: 380.0,
                    PV: 0.0,
                    AllPv: 0.0
                }
            ],
        };
    },
    methods: {
        //
        resetForm() {            
        },
        //
        onSubmit() {
            let routeData = this.$router.resolve({
                path: "/addMemberForm",
                query:{id:this.formMember.id}
            });
            window.open(routeData.href, '_blank');
        },
        //
        searchCode() {
            //
            this.$axios.get("/apis/member/findByMCode", {
                params:{
                    mCode:this.formSearch.id,
                }
            })
            .then(response=>{
                if(response.data.code){
                    this.formData.push(response.data.data);
                    this.searchUser = true;    
                    this.loadingTable = false;
                }else {
                    //
                    // util.$emit("userDefined",{
                    //     icon:"error",
                    //     title:response.data.msg
                    // });
                }
            })
            
        },
        //
        getCurrentRow(val) {
            this.selectNum = val;
            this.isdisabled = false;
        },
        //
        sendData() {
            this.searchUser = false;
            this.formSearch.name = this.formData[this.selectNum].mname;
            this.formSearch.nickname = this.formData[this.selectNum].mnickname;
        },
        //
        onSearch2() {
            this.searchUser = true;
        },
        //
        onSearch3() {
            this.searchUser = true;
        },
        //
        addBtn(index, items) {
            items.number++;
        },
        //
        reduceBtn(index, items) {
            if(items.number === 0) {
                return;
            }
            items.number--;
        },
    }
};
</script>

<style>
.line {
    width: 100%;
    position: relative;
    display: inline-block;
    border: 1px solid #888;
    border-radius: 10px;
    padding: 20px;
    margin-bottom: 30px;
}

.line > span {
    position: absolute;
    top: -10px;
    padding: 0 10px;
    background: #fff;
}
.serch-input .el-form-item__content {
    position: relative;
}
.serch-input .el-icon-search {
    position: absolute;
    right: 10px;
    top: 12px;
    font-size: 18px;
    color: #666;
    cursor: pointer;
}
.el-icon-search:hover {
    color: #409eff;
}
.area-select.medium {
    width: 90px;
    margin-top: 7px;
}
.area .area-select-wrap .area-select {
    height: 28px;
}
.area-select-wrap .area-selected-trigger {
    line-height: 28px;
    text-align: center;
    padding: 0;
}
.text-center .el-form-item__label {
    text-align: center;
}

.number-count {
    padding: 0 10px;
}
.btn-center .el-form-item__content {
    display: flex;
    justify-content: space-around;
}
.wrap .el-button--primary.is-plain {
    height: 22px;
    line-height: 22px;
    padding: 0px 10px;
}
</style>



