<template>
    <el-form  :model="form" label-width="80px" label-position="left">
        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-date-picker 
                        v-model="form.time" 
                        type="daterange" 
                        range-separator="-"
                        start-placeholder=""
                        end-placeholder=""
                        format="yyyy-MM-dd"
                        :default-time="['00:00:00', '23:59:59']"
                        :picker-options="pickerOptions"
                        @change="onChangeDate">
                    </el-date-picker>
                </el-form-item>
            </el-col>
            <el-col :span="5" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.name"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="4" :offset="1">
                <el-button type="primary" @click="onSearch"></el-button>
                <el-button @click="exportExcel"></el-button>
            </el-col>
        </el-row> 
        <el-row>
            <el-col :span="5">
                <el-form-item label="">
                    <el-input v-model="form.tel"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="5" :offset="2">
                <el-form-item label="">
                    <el-select v-model="form.state" placeholder="" >
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                    </el-select>
                </el-form-item>
            </el-col>
        </el-row>          

        <el-row>
            <el-col :span="24">
                <el-table 
                    :data="searchData" 
                    
                    id="memberTable" 
                    v-loading="loadingTable" 
                    element-loading-text=""
                    element-loading-spinner="el-icon-loading">
                    <el-table-column prop="mCode" label="" align="center" width="80px">
                    </el-table-column>
                    <el-table-column prop="mCode" label="" align="center" width="80px">
                    </el-table-column>
                    <el-table-column prop="mCode" label="" align="center" width="80px"> 
                    </el-table-column>
                    <el-table-column prop="mCode" label="" align="center" width="80px">
                    </el-table-column>
                    <el-table-column prop="mCode" label="" align="center" width="90px">
                    </el-table-column>
                    <el-table-column prop="mCode" label="" align="center" width="90px">
                    </el-table-column>
                    <el-table-column prop="mCode" label="" align="center" width="100px">
                    </el-table-column>
                    <el-table-column prop="mCode" label="" align="center" width="100px">
                    </el-table-column>
                    <el-table-column prop="mCode" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="mCode" label="PV" align="center" width="100px">
                    </el-table-column>
                    <el-table-column prop="mCode" label="" align="center" width="110px">
                    </el-table-column>
                    <el-table-column prop="mCode" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="mCode" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="mCode" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="mCode" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="mCode" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="mCode" label="" align="center">
                    </el-table-column>
                    <el-table-column label="" align="center" width="140px">
                        <template slot-scope="scope">
                            <el-button type="text" @click="onShowDetails(scope.row)"></el-button>
                            <el-button type="text" @click="onShowDetails(scope.row)"></el-button>
                            <el-button type="text" @click="onShowDetails(scope.row)" class="cancel"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <br>
        <el-row type="flex" justify="center">
            <el-col :span="8" align="center">
                <el-pagination
                    :page-size="pageData.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :page-sizes="[10, 20, 30, 50,pageData.total]"
                    :total="pageData.total"
                    :current-page="pageData.currentPage"
                    @current-change="onChangePage"  
                    @size-change="handleSizeChange">
                </el-pagination>
            </el-col>
        </el-row>

        <!--  -->
        <dialog-com></dialog-com>

    </el-form>
</template>


<script>
export default {
    data() {
        let time1 = new Date();
        return {
            loadingTable:false, //
            form: {
                time: ["",""], //
                name: "", //
                tel: "", //
                state: "" //
            },
            //
            searchData: [],
            //
            pageData:{
                currentPage:1,
                pageSize:10,
                total:null,
            },
            //
            pickerOptions: {
                shortcuts: [{
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                    }
                },
                {
                    text: '',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                        picker.$emit('pick', [start, end]);
                    }
                }, 
                {
                    text: '',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                        picker.$emit('pick', [start, end]);
                    }
                }]
            }
            
        };
    },
    methods: {
        //
        onChangePage(currentPage) {
            this.form.currentPage = currentPage;
            this.onSearch();
        },
        //
        handleSizeChange(pageSize) {
            this.pageData.pageSize = pageSize;
            this.onSearch();
        },
        //
        onChangeDate( data) {
            if(data) {
                this.form.time[0] = data[0].getFullYear()+'-'+(data[0].getMonth()+1)+'-'+data[0].getDate()+' '+data[0].getHours()+':'+data[0].getMinutes()+':'+ data[0].getSeconds()
                this.form.time[1] = data[1].getFullYear()+'-'+(data[1].getMonth()+1)+'-'+data[1].getDate()+' '+data[1].getHours()+':'+data[1].getMinutes()+':'+ data[1].getSeconds()
            }
       },
        //
        onSearch() {
            this.loadingTable = true;  
            //
            let timeStart = "";
            let timeEnd = "";
            if(this.form.time){
                timeStart = this.form.time[0];
                timeEnd = this.form.time[1];
            }
            this.$axios({
                method:'post',
                url:"/apis/member/findEditStatus",
                params:{
                    currentPage:this.form.currentPage,
                    pageSize:this.pageData.pageSize,
                    mCode:this.form.id,
                    mName:this.form.name,
                    updateType:this.form.type,
                    reviewStatus:this.form.state,
                    updateTimeStar:timeStart,
                    updateTimeEnd:timeEnd,
                    date:new Date().getTime()
                }
            })     
            .then(response=>{
                if(response.data.code){
                    this.searchData = response.data.data.list;
                    for(var i = 0; i< this.searchData.length; i++ ){
                        if(this.searchData[i].reviewStatus==0){
                            this.searchData[i].reviewStatus="";
                        }else if(this.searchData[i].reviewStatus==1){
                            this.searchData[i].reviewStatus="";
                        }else if(this.searchData[i].reviewStatus==2){
                            this.searchData[i].reviewStatus="";
                        }else{
                            this.searchData[i].reviewStatus="";
                        }

                        if(this.searchData[i].updateType==0){
                            this.searchData[i].updateType="";
                        }else if(this.searchData[i].updateType==1){
                            this.searchData[i].updateType="";
                        }else if(this.searchData[i].updateType==2){
                            this.searchData[i].updateType="";
                        }else if(this.searchData[i].updateType==3){
                            this.searchData[i].updateType="";
                        }else if(this.searchData[i].updateType==4){
                            this.searchData[i].updateType="";
                        }else if(this.searchData[i].updateType==5){
                            this.searchData[i].updateType="";
                        }else{
                            this.searchData[i].updateType="";
                        }
                    }
                    this.pageData.currentPage = response.data.data.pageNum,
                    this.pageData.pageSize = response.data.data.pageSize,
                    this.pageData.total = response.data.data.total
                }
                setTimeout(()=>{
                    this.loadingTable = false;
                },200)
            })
        },
        //
        onShowDetails(data) {
            // util.$emit("DialoChangeDetails",{
            //     data:data,
            //     showSubmit:true
            // });
        },
        //
        exportExcel() {                  
            if(this.searchData.length==0){
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                });
            }else {
                new Promise((resolve,reject)=>{
                    this.pageData.pageSize = this.pageData.total;
                    this.onSearch();
                    setTimeout(()=>{
                        resolve();
                    },500)
                })
                .then(()=>{
                    var wb = XLSX.utils.table_to_book(
                        document.querySelector("#memberTable")
                    );
                    var wbout = XLSX.write(wb, {
                        bookType: "xlsx",
                        bookSST: true,
                        type: "array"
                    });
                    try {
                        FileSaver.saveAs(
                            new Blob([wbout], { type: "application/octet-stream" }),
                            ".xlsx"
                        );
                    } catch (e) {
                        if (typeof console !== "undefined") console.log(e, wbout);
                    }
                    this.pageData.pageSize = 10;
                    this.onSearch();
                    return wbout;
                })
                
            }
        },
    },
    created() {
        //this.onSearch();
        util.$on("ChangeDetailsSuccess",()=>{
            this.onSearch();
        });
    }
};
</script>

<style scoped>
.cancel{
    color: red;
}
</style>



