<template>
    <el-form  label-width="80px" label-position="left">
        <el-row type="flex" justify="space-between">
            <el-col :span="14" class="top-title">
                <span><b>{{periodCode}}</b></span>  
                <span>PV{{sum}}</span>
                <span>VIP{{sumVIP}}</span>
            </el-col>
            <el-col :span="4" align="right">
                <el-button type="primary" @click="onCount"> </el-button>
            </el-col>
        </el-row>
        <br>
        <el-row>
            <el-col :span="24">
                <el-table 
                    :data="tableData" 
                    v-loading="loadingTable" 
                    element-loading-text=","
                    element-loading-spinner="el-icon-loading">
                    <el-table-column prop="periodCode" label="" fixed align="center">
                    </el-table-column>
                    <el-table-column prop="mCode" label="" align="center"> 
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="bonusRetail" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="bonusNewVip" label="VIP" align="center" width="130">
                    </el-table-column>
                    <el-table-column prop="bonusDevp1" label="1"  align="center" width="130">
                    </el-table-column>
                    <el-table-column prop="bonusDevp2" label="2" align="center" width="130">
                    </el-table-column>
                    <el-table-column prop="bonusDevpShare" label="" align="center" width="130">
                    </el-table-column>
                    <el-table-column prop="bonusDevpSum" label="" align="center" width="130">
                    </el-table-column>
                    <el-table-column prop="bonusLdDirect" label="" align="center" width="140">
                    </el-table-column>
                    <el-table-column prop="bonusLdIndirect" label="" align="center" width="140">
                    </el-table-column>
                    <el-table-column prop="bonusLdSupport" label="" align="center" width="140">
                    </el-table-column>
                    <el-table-column prop="bonusLdSum" label="" align="center" width="110">
                    </el-table-column>
                    <el-table-column prop="chargeService" label="" align="center" width="110">
                    </el-table-column>
                    <el-table-column prop="bonusSum" label="" align="center" width="110">
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <br>
        <el-row type="flex" justify="center">
            <el-col :span="8" align="center">
                <el-pagination
                    :page-size="pageData.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :page-sizes="[10, 20, 30, 50,pageData.total]"
                    :total="pageData.total"
                    :current-page="pageData.currentPage"
                    @current-change="onChangePage"  
                    @size-change="handleSizeChange">
                </el-pagination>
            </el-col>
        </el-row>
    </el-form>
</template>


<script>
export default {
    data() {
        return {
            submitDisable:false, 
            loadingTable:false, //
            periodCode:"",  //
            sumVIP:0,   //VIP
            sum:0,   //
            form: {
                id: "", //
                name: "", //
            },
            //
            tableData: [],
            //
            pageData:{
                currentPage:1,
                pageSize:10,
                total:null,
            },
        };
    },
    methods: {
        //
        onSearch() {
            this.loadingTable = true;  
            this.$axios({
                method:'get',
                url:"/apis/bonus/findBonus",
                params:{
                    periodCode:this.periodCode,
                    currentPage:this.pageData.currentPage,
                    pageSize:this.pageData.pageSize,
                    date:new Date().getTime()
                }
            })     
            .then(response=>{
                if(response.data.code){ 
                    this.tableData = response.data.data.list;
                    this.pageData.total = response.data.data.total;
                    this.pageData.currentPage = response.data.data.pageNum;
                    this.pageData.pageSize = response.data.data.pageSize;
                    this.tableData = response.data.data.list;
                    for(var i in this.tableData){
                        this.sumVIP += this.tableData[i].bonusNewVip;
                        this.sum += this.tableData[i].bonusSum;
                    }
                }
                setTimeout(()=>{
                    this.loadingTable = false;
                },200)
            })
        },
        //
        onChangePage(currentPage) {
            this.form.currentPage = currentPage;
            //this.onSearch();
        },
        //
        handleSizeChange(pageSize) {
            this.pageData.pageSize = pageSize;
            //this.onSearch();
        },
        //
        onCount() { 
            this.submitDisable = true; 
            this.loadingTable = true;
            this.$axios({
                method:'get',
                url:"/apis/bonus/countBonusByPeriod",
                params:{
                    periodCode:this.periodCode,
                }
            })     
            .then(response=>{
                if(response.data.code) {
                    setTimeout(()=>{
                        this.$message({
                            showClose: true,
                            message: '',
                            type: 'success'
                        });
                        this.onSearch();  
                    },1000)
                }else {
                    this.$message({
                        showClose: true,
                        message: response.data.msg,
                        type: 'error'
                    });
                }
            })
        }    
    },
    created() {
        this.periodCode = this.$route.query.periodCode;
        this.onSearch();
    }
};
</script>
<style scoped>
.top-title span{
    display: inline-block;
    margin-right: 30px;
}
</style>




