<template>
    <el-form ref="form" :rules="rules" :model="form" label-width="110px" label-position="left">

        <el-form-item label="">
            <el-button type="primary" icon="el-icon-search"  @click="onSearch"></el-button>
        </el-form-item>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.id" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.name" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>      

        <el-row>
            <el-col :span="6">
                <el-form-item label=":">
                    <el-input v-model="form.currentType"  disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="" prop="nextType">
                    <el-select v-model="form.nextType" placeholder="">
                        <el-option v-for="(items,index) in select" :key="index" :label="items" :value="items" :disabled="form.currentType==items"></el-option>
                    </el-select>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="8">
                <el-form-item label="" prop="desc">
                    <el-input type="textarea" v-model="form.desc" :autosize="{ minRows: 4, maxRows: 6}"></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-form-item>
            <el-button type="primary" :loading="submitLoading" @click="onSubmit('form')"></el-button>
        </el-form-item>

        <!--  -->
        <dialog-com ref="dialog" @searchData="getSearchData"></dialog-com>
    </el-form>
</template>


<script>
export default {
    data() {
        return {
            submitLoading:false,  //loading
            select:['','VIP','','','','','','','',''],
            form: {
                id: "", //
                name: "", //
                currentType:"", //
                nextType:"", //
                desc: "" //
            },
            //
            rules: {
                nextType: [
                    { required: true, message: "", trigger: ['blur','change'] },
                ],
                desc: [
                    { required: true, message: "", trigger: ['blur','change'] },
                ]
            }
        };
    },
    methods: { 
        //
        onSubmit(form) {
            if(!this.form.id) {     //
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });       
            }else{
                this.$refs[form].validate((valid) => {
                    if (valid) {
                        this.submitLoading = true;
                        this.$axios({
                            method:'post',
                            url:"/apis/member/updateRelationByMCode",
                            params: {
                                mCode:this.form.id,
                                mName:this.form.name,
                                rankName:this.form.currentType, 
                                rankNameNew:this.form.nextType,
                                mDesc:this.form.desc
                            }
                        })
                        .then(response=>{
                            if(response.data.code){
                                this.$refs.dialog.userDefined({
                                    icon:"success",
                                    title:",!"
                                });
                            } else{
                                this.$refs.dialog.userDefined({
                                    icon:"error",
                                    title:response.data.msg
                                });
                            }
                            this.submitLoading = false;
                        })
                    } else {
                        this.$message({
                            showClose: true,
                            message: '!',
                            type: 'error'
                        });
                        return false;
                    }
                }); 
            }
        },
        //
        onSearch() {
            this.$refs.dialog.onSearchDialog();
        },
        //
        getSearchData(data) {
            this.form.currentType = "";
            this.form.nextType = "";
            this.form.desc = "";
            this.form.id = data.mCode;
            this.form.name = data.mName;
            this.form.currentType = data.mLevel;
        }
    }
};
</script>

<style>
</style>



