<template>
    <el-form  label-width="80px" label-position="left">
        <el-row>
            <el-col :span="24" align="right">
                <el-button type="primary" @click="showDialogAdd"></el-button>
            </el-col>
        </el-row>
        <br/>
        <el-row>
            <el-col :span="24">
                <el-table 
                    :data="tableData" 
                    v-loading="loadingTable" 
                    element-loading-text=""
                    element-loading-spinner="el-icon-loading">
                    <el-table-column prop="periodCode" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="beginDate" label="" align="center"> 
                    </el-table-column>
                    <el-table-column prop="endDate" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="salesStatus" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="calStatus" label="" align="center" width="160px">
                    </el-table-column>
                    <el-table-column prop="calTimes" label="" align="center" width="160px">
                    </el-table-column>
                    <el-table-column prop="bonusStatus" label="" align="center" width="160px">
                    </el-table-column>
                    <el-table-column label="" align="center" width="180px">
                        <template slot-scope="scope">
                            <el-button size="mini" type="success" :disabled="tableData[scope.$index].salesStatus==''"  @click="showDialogChange(scope.row,scope.$index)"></el-button>
                            <el-button size="mini" type="warning" :disabled="tableData[scope.$index].calStatus!=''" @click="showDialogSwitch(scope.row)"></el-button>
                            <el-button size="mini" type="danger" :disabled="tableData[scope.$index].salesStatus==''" @click="showDialogDelete(scope.row)" class="delete"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <br>
        <el-row type="flex" justify="center">
            <el-col :span="8" align="center">
                <el-pagination
                    :page-size="pageData.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :page-sizes="[10, 20, 30, 50,pageData.total]"
                    :total="pageData.total"
                    :current-page="pageData.currentPage"
                    @current-change="onChangePage"  
                    @size-change="handleSizeChange">
                </el-pagination>
            </el-col>
        </el-row>

        <!--  -->
        <el-dialog title=""  :visible.sync="DialogChange" width="450px" center>
            <el-form status-icon  :model="formChange" :rules="rules" ref="formChange"  label-width="80px" label-position="left">
                <el-form-item label="">
                    <el-input v-model="formChange.computingCycle" disabled></el-input>
                </el-form-item>
                <el-form-item label="" prop="dateStart">
                    <el-date-picker 
                        v-model="formChange.dateStart" 
                        :picker-options="startDateChange" 
                        format="yyyy-MM-dd" 
                        value-format="yyyy-MM-dd"  
                        type="date" 
                        placeholder=""
                        :disabled="isStarted">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="" prop="dateEnd">
                    <el-date-picker v-model="formChange.dateEnd" :picker-options="endDateChange" format="yyyy-MM-dd" value-format="yyyy-MM-dd"  type="date" placeholder=""></el-date-picker>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="DialogChange = false"> </el-button>
                <el-button type="primary" @click="onChangeCycle('formChange')"> </el-button>
            </span>
        </el-dialog>

        <el-dialog title=""  :visible.sync="DialogAdd" width="450px" center>
            <el-form status-icon :rules="rules" :model="formAdd" ref="formAdd"  label-width="80px" label-position="left">
                <el-form-item label="" prop="year">
                    <el-date-picker v-model="formAdd.year" :picker-options="yearOpt" value-format="yyyy" format="yyyy" @change="onChangeYear"   type="year" placeholder=""></el-date-picker>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="formAdd.computingCycle" disabled=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="dateStart">
                    <el-date-picker v-model="formAdd.dateStart" :picker-options="startDateAdd" format="yyyy-MM-dd" value-format="yyyy-MM-dd"  type="date" placeholder=""></el-date-picker>
                </el-form-item>
                <el-form-item label="" prop="dateEnd">
                    <el-date-picker v-model="formAdd.dateEnd" :picker-options="endDateAdd"  format="yyyy-MM-dd" value-format="yyyy-MM-dd"  type="date" placeholder=""></el-date-picker>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="DialogAdd = false"> </el-button>
                <el-button type="primary" @click="onAddCycle('formAdd')"> </el-button>
            </span>
        </el-dialog>

        <el-dialog title=""  :visible.sync="DialogSwitch" width="450px" center>
            <el-form label-width="80px" label-position="left">
                <el-form-item label="">
                    <el-input type="textarea" v-model="formSwitch.desc" :autosize="{ minRows: 4}"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="DialogSwitch = false"> </el-button>
                <el-button type="primary" @click="onSwitchCycle('formSwitch')"> </el-button>
            </span>
        </el-dialog>
    </el-form>
</template>


<script>
export default {
    data() {
        return {
            loadingTable:false, //
            DialogChange:false, 
            DialogAdd:false,
            DialogSwitch:false,
            isStarted:false, //
            cycleDelete:"", //
            //
            tableData: [],
            //
            pageData:{
                currentPage:1,
                pageSize:10,
                total:null,
            },
            formChange:{
                computingCycle:"",  //
                selectStart:"", //
                selectEnd:"",   //
                dateStart:"",   //
                dateEnd:"",     //
            },
            formAdd:{
                year:"",
                computingCycle:"",
                prePeriod:"", //
                selectStart:"", 
                dateStart:"",
                dateEnd:"",
            },
            formSwitch:{
                computingCycle:"",
                desc:""
            },
            //
            rules: {
                year: [
                    { required: true, message: "", trigger: 'blur' }
                ],
                dateStart: [
                    { required: true, message: "", trigger: 'blur' }
                ],
                dateEnd: [
                    { required: true, message: "", trigger: 'blur' }
                ]
            },
            /*
            *
            */
           yearOpt:{
                disabledDate: (time)=> {
                    let curDate = (new Date()).getTime();
                    let Year =  1 * 365 * 24 * 3600 * 1000;
                    let endYear = curDate + Year;
                    return time.getTime() < new Date() || time.getTime() >= endYear;
                }
            },
            startDateAdd:{
                disabledDate: (time)=> {
                    let date = this.formAdd.dateEnd.split("-");
                    let selectStart = this.formAdd.selectStart.split("-");
                    return time.getTime() >= new Date( date[0], date[1]-1, date[2]) || time.getTime() <= new Date( selectStart[0],selectStart[1]-1,selectStart[2]);
                }
            },
            endDateAdd: {
                disabledDate: (time)=> {
                    let date = this.formAdd.dateStart.split("-");
                    return time.getTime() <= new Date( date[0], date[1]-1, date[2]);
                }
            },
            startDateChange:{
                disabledDate: (time)=> {
                    let date = this.formChange.dateEnd.split("-");
                    let selectStart = this.formChange.selectStart.split("-");
                    return time.getTime() <= new Date( selectStart[0],selectStart[1]-1,selectStart[2]) || time.getTime() >= new Date( date[0], date[1]-1, date[2]);
                }
            },
            endDateChange: {
                disabledDate: (time)=> {
                    let date = this.formChange.dateStart.split("-");
                    if(this.formChange.selectEnd!=""){
                        let selectEnd = this.formChange.selectEnd.split("-");
                        return time.getTime() <= new Date( date[0], date[1]-1, date[2]) || time.getTime() >= new Date( selectEnd[0],selectEnd[1]-1,selectEnd[2]);
                    }else {
                        //,
                        return time.getTime() <= new Date( date[0], date[1]-1, date[2]);
                    }
                }
            },
        };
    },
    methods: {
        //
        onSearch() {
            this.loadingTable = true;  
            this.$axios({
                method:'get',
                url:"/apis/member/findPeriodAll",
                params:{
                    currentPage:this.pageData.currentPage,
                    pageSize:this.pageData.pageSize,
                    date:new Date().getTime()
                }
            })     
            .then(response=>{
                if(response.data.code){
                    this.pageData.currentPage = response.data.data.pageNum;
                    this.pageData.pageSize = response.data.data.pageSize;
                    this.pageData.total = response.data.data.total;
                    let list = response.data.data.list;
                    for(let i= 0; i < list.length; i++){
                        //
                        list[i].beginDate = list[i].beginDate.slice(0,10);
                        list[i].endDate = list[i].endDate.slice(0,10);
                        //
                        if(list[i].calStatus==0){
                            list[i].calStatus = "";
                        }else if(list[i].calStatus==1){
                            list[i].calStatus = "";
                        }else if(list[i].calStatus==2){
                            list[i].calStatus = "";
                        }else if(list[i].calStatus==3){
                            list[i].calStatus = "";
                        }
                        //
                        if(list[i].salesStatus==0){
                            list[i].salesStatus = "";
                        }else if(list[i].salesStatus==1){
                            list[i].salesStatus = "";
                        }else if(list[i].salesStatus==2){
                            list[i].salesStatus = "";
                        }else if(list[i].salesStatus==3){
                            list[i].salesStatus = "";
                        }
                        //
                        if(list[i].bonusStatus==0){
                            list[i].bonusStatus = "";
                        }else if(list[i].bonusStatus==1){
                            list[i].bonusStatus = "";
                        }
                    }
                    this.pageData.currentPage = response.data.data.pageNum;
                    this.pageData.pageSize = response.data.data.pageSize;
                    this.tableData = response.data.data.list;
                }
                setTimeout(()=>{
                    this.loadingTable = false;
                },200)
            })
        },
        //
        onChangePage(currentPage) {
            this.form.currentPage = currentPage;
            this.onSearch();
        },
        //
        handleSizeChange(pageSize) {
            this.pageData.pageSize = pageSize;
            this.onSearch();
        },
        //
        showDialogAdd() {
            this.DialogAdd = true;
            //  
            if(this.tableData.length >0){
                let curYear = parseInt(this.tableData[0].periodCode/100);   //
                let number = parseInt(this.tableData[0].periodCode.substr(4,2))+1;   //+1
    
                this.formAdd.computingCycle =  ""+ curYear + number;  //
                this.formAdd.selectStart = this.tableData[0].endDate; //
                this.formAdd.prePeriod = this.tableData[0].periodCode;  //
                this.formAdd.year = String(curYear);
            }else {
                let date = new Date();
                this.formAdd.computingCycle =  date.getFullYear() + "01";  //
                this.formAdd.selectStart = date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate(); //
                this.formAdd.prePeriod = date.getFullYear()+"00";  //
                this.formAdd.year = String(date.getFullYear())
            }   
            this.formAdd.dateStart = "";
            this.formAdd.dateEnd = "";
        },
        //
        onChangeYear(val) {
            //,
            let isNewYear = false;
            for(var i = 0; i < this.tableData.length; i++){
                let year = parseInt(this.tableData[0].periodCode/100);
                if(year !=val){
                    isNewYear = true; 
                    break;
                }
            }
            if(val!=(new Date().getFullYear())&&isNewYear){
                this.formAdd.computingCycle = val +"01";
            }else {
                //
                let number = parseInt(this.tableData[0].periodCode.substr(4,2))+1;
                this.formAdd.computingCycle = ""+val + number ;
            }
        },
            
        //
        onAddCycle(form) {
            this.$refs[form].validate((valid) => {
                if (valid) {
                    this.$axios({
                        method:'post',
                        url:"/apis/member/addPeriod",
                        params: {
                            periodCode:this.formAdd.computingCycle,
                            beginDateS:this.formAdd.dateStart,
                            endDateS:this.formAdd.dateEnd,
                            prePeriod:this.formAdd.prePeriod
                        }
                    })
                    .then(response=>{
                        if(response.data.code){
                            this.$message({
                                showClose: true,
                                message: '!',
                                type: 'success'
                            });
                            this.onSearch(); 
                        } else{
                            this.$message({
                                showClose: true,
                                message: response.data.msg,
                                type: 'error'
                            });
                        }
                        this.DialogAdd = false;
                    }) 
                }else {
                    this.$message({
                        showClose: true,
                        message: '!',
                        type: 'error'
                    }); 
                    return false;
                }
            });
        },

        //
        showDialogChange(row,index) {
            this.formChange.selectStart = "";
            this.formChange.selectEnd = "";
            this.DialogChange = true;
            //
            if(row.salesStatus==''){
                this.isStarted = true;
            }else {
                this.isStarted = false;
            }
            //1
            if(index!=0){
                this.formChange.selectStart = this.tableData[index+1].endDate; //()
                this.formChange.selectEnd = this.tableData[index-1].beginDate; //()
            }else {
                this.formChange.selectStart = this.tableData[index+1].endDate; //()
            }
            this.formChange.computingCycle = row.periodCode;
            this.formChange.dateStart = row.beginDate;
            this.formChange.dateEnd = row.endDate;
        },
        //
        onChangeCycle(form) {
            this.$refs[form].validate((valid) => {
                if (valid) {
                    this.$axios({
                        method:'post',
                        url:"/apis/member/editPeriod",
                        params: {
                            periodCode:this.formChange.computingCycle,
                            beginDateS:this.formChange.dateStart,
                            endDateS:this.formChange.dateEnd,
                        }
                    })
                    .then(response=>{
                        if(response.data.code){
                            this.$message({
                                showClose: true,
                                message: '!',
                                type: 'success'
                            }); 
                            this.onSearch();
                        } else{
                            this.$message({
                                showClose: true,
                                message: response.data.msg,
                                type: 'error'
                            });
                        }
                        this.DialogChange = false;
                    }) 
                }else {
                    this.$message({
                        showClose: true,
                        message: '!',
                        type: 'error'
                    }); 
                    return false;
                }
            });
        },
        //
        showDialogSwitch(row) {
            this.DialogSwitch = true;
            this.formSwitch.computingCycle = row.periodCode;
            this.formSwitch.desc = "";
        },
        //
        onSwitchCycle(form) {
            this.$axios({
                method:'post',
                url:"/apis/member/editPeriodStatu",
                params: {
                    periodCode:this.formSwitch.computingCycle,
                    updateMemo:this.formSwitch.desc
                }
            })
            .then(response=>{
                if(response.data.code){
                    this.$message({
                        showClose: true,
                        message: '!',
                        type: 'success'
                    }); 
                    this.onSearch();
                } else{
                    this.$message({
                        showClose: true,
                        message: response.data.msg,
                        type: 'error'
                    });
                }
                    this.DialogSwitch = false;
            }) 
        },
        //
        showDialogDelete(row) {
            this.cycleDelete = row.periodCode;
            this.$confirm('?', '', {
                confirmButtonText: ' ',
                cancelButtonText: ' ',
                type: 'warning',
                center: true
            }).then(() => {
                this.onDelete();
            }).catch(() => {});
        },
        //
        onDelete() {
             this.$axios({
                    method:'get',
                    url:"/apis/member/delPeriod",
                    params: {
                        periodCode:this.cycleDelete
                    }
                })
                .then(response=>{
                    if(response.data.code){
                        this.$message({
                            showClose: true,
                            message: '!',
                            type: 'success'
                        }); 
                        this.onSearch();
                    } else{
                        this.$message({
                            showClose: true,
                            message: response.data.msg,
                            type: 'error'
                        });
                    }
                }) 
        }
    },
    created() {
        this.onSearch();
    }
};
</script>

<style scoped>
.el-date-editor.el-input, .el-date-editor.el-input__inner {
    width:100%;
}
</style>



